/**
* Format Color in Polylines
* @param color 
*/
const optionsRect = (status:string, position:POSITION[]) => {

  let color = COLORSTATUS[status];

  return {
      path            : position,
      colorLine       : color,
      geodesic        : true,
      strokeColor     : color,
      strokeOpacity   : 0.8,
      strokeWeight    : 3,
      fillColor       : color,
      fillOpacity     : 0.8
  }
}


/**
* Get PMPSM Data
*/
const getLatLongPMPSM = async () => {
  await axios.get(CONSTAPIS.APIS.maps.getAllLatLngSM, { timeout : 10000 }).then((response)=>{
      if(project.value === 'SmartLinkAntamina' ||
          project.value === 'SmartLinkTest' ||
          project.value === 'SmartLinkZaldivar' ||
          project.value === 'SmartLinkAntucoya'){
          elementPMPSM.value = response.data.body;
      }
      if( project.value === 'SmartLinkMasterFront' || 
          project.value === 'SmartLinkMaster' || 
          project.value === 'SmartLinkCananea' ||
          project.value === 'SmartLinkCMDIC' ||
          project.value === 'SmartLinkIOT'
       ){
          elementPMPSM.value = response.data;
      }
  })
  .catch(function (error) {
      showConsoleError(error);
  });
}


const getAllIEOByIP = ref([
  { ip: "" , ok: 0, alarm : 0, alert: 0, down: 0 },
]);


const getStatusIEO = (ipSearch:string) => {
  let ieo = 0;
  let total = 1;
  const findObject = getAllIEOByIP.value.find((objeto) => objeto.ip === ipSearch)
  if(findObject) {
      total = findObject.ok + findObject.alarm + findObject.alert + findObject.down
      ieo = parseFloat((findObject.ok/total).toFixed(2))
  }
  return ieo
}

const getColorSheet = (status:string) => {
  let colorSheet = '';
  if(status.toUpperCase() === 'OK')       { colorSheet = 'green'}
  if(status.toUpperCase() === 'ALARM')    { colorSheet = 'yellow'}
  if(status.toUpperCase() === 'ALERT')    { colorSheet = 'red'}
  if(status.toUpperCase() === 'DOWN')     { colorSheet = '#505150'}
  return colorSheet
}
const getColorCard = (status:string) => {
  let colorSheet = '';
  if(status.toUpperCase() === 'OK')       { colorSheet = 'greenSheet'}
  if(status.toUpperCase() === 'ALARM')    { colorSheet = 'yellowSheet'}
  if(status.toUpperCase() === 'ALERT')    { colorSheet = 'redSheet'}
  if(status.toUpperCase() === 'DOWN')     { colorSheet = 'greySheet'}
  return colorSheet
}

const getPesos = (status:string) => {
  let peso = 0;
  if(status.toUpperCase() === 'OK')       peso = 1;
  if(status.toUpperCase() === 'ALARM')    peso = 2;
  if(status.toUpperCase() === 'ALERT')    peso = 3;
  if(status.toUpperCase() === 'DOWN')     peso = 4;
  return peso
}

