const getAllStatusAndLocation = async() => {
  markerspmpap.value.forEach((item:any) => {
      if(!allFinalItems.value[item.ip]) {
          allFinalItems.value[item.ip] = {}
      }
      allFinalItems.value[item.ip] = {
          position : item.position,
          status   : item.status,
      }
  })

  markerspmpsm.value.forEach((item:any) => {
      if(!allFinalItems.value[item.ip]) {
          allFinalItems.value[item.ip] = {}
      }
      allFinalItems.value[item.ip] = {
          position : item.position,
          status   : item.status,
      }
  })
  
  markersrajant.value.forEach((item:any) => {
      if(!allFinalItems.value[item.ip]) {
          allFinalItems.value[item.ip] = {}
      }
      allFinalItems.value[item.ip] = {
          position : item.position,
          status   : item.status,
      }
  })
}

const matchPMPSMWithRAJANT = async() =>{
    let i = 0.00003;
    
    elementPMPSM.value.forEach((pmpsm:EQUIPAMENTS)=>{
        i = i + 0.00003;
        const ipRaj         = ref('');
        const typeRaj       = ref('');
        const finalPosition = ref([0,0]);
        const equipFindRAJ  = ref(false);
        const nameRaj   = ref('');
        let anotacion   = pmpsm.anotacion !== null ? JSON.parse(pmpsm.anotacion??'{}') : {};
        if(anotacion.gps){
          console.log('entro a ANOTACION')
            markersrajant.value.find(({ip,position}) => {
                if(anotacion.gps == ip) {
                    finalPosition.value = position
                }
            })
        }
        else{
            if(pmpsm.latitud && pmpsm.longitud){
                finalPosition.value = [pmpsm.latitud,pmpsm.longitud];               
            }
 
            elementRAJANT.value.find(({name,latitud,longitud,ip,tipo}:EQUIPAMENTS)=>{
                if(name && latitud && longitud){
                    let firstName   = '';
                    let secondName  = '';

                    if(project.value === 'SmartLinkCananea') {
                        firstName   = name.split('-')[0];
                        secondName  = pmpsm.name?.split('M')[1]??'';
                    }
                    if(project.value === 'SmartLinkCMDIC') {
                        firstName   = name.split('-')[2];
                        secondName  = pmpsm.name?.split('-')[3]??'';
                    }
                    if(firstName === secondName){
                        equipFindRAJ.value  = true;
                        let finalPositions  = [latitud, longitud];
                        finalPosition.value = finalPositions;
                        nameRaj.value       = name;
                        ipRaj.value         = ip??'';
                        typeRaj.value       = tipo??'';
                    }
                }
            })
        }
        

        markerspmpsm.value.push({
            position: finalPosition.value,
            name    : pmpsm.name,
            label   : pmpsm.name,
            ip      : pmpsm.ip,
            tipo    : pmpsm.tipo,
            status  : getStatusEquipment(pmpsm.ip??''),
        })
    })

    finalALLITEMS.value = [...markersrajant.value, ...markerspmpap.value, ...markerspmpsm.value]
    console.log(finalALLITEMS.value)
}

const markersNear = async() => {

  finalALLITEMS.value.forEach((item:any) => {
    finalALLITEMS.value.find(({position,ip}:any)=>{

      if(item.ip !== ip && Math.abs(item.position[0] - position[0]) <= 0.0001 && Math.abs(item.position[1] - position[1]) <= 0.0001) {
        console.log(item.ip, ip, 'estan cerca')
      }

    })
  })
}

const getAllPolylines = async() => {
  await getAllStatusAndLocation();
  await axios.get(CONSTAPIS.APIS.maps.getAllPolylines, { timeout : 10000 }).then((resp) => {
      let allRelations =resp.data
      allRelations.forEach((item:any) => {
          let positionPadre = allFinalItems.value[item.ip_fuente] ? allFinalItems.value[item.ip_fuente].position : [0,0]
          let statusPadre = allFinalItems.value[item.ip_fuente] ? allFinalItems.value[item.ip_fuente].status : 'down'
          let positionHijo = allFinalItems.value[item.ip_cliente] ? allFinalItems.value[item.ip_cliente].position : [0,0]
          let statusHijo = allFinalItems.value[item.ip_cliente] ? allFinalItems.value[item.ip_cliente].status : 'down'
          
          let pesoPadre = getPesos(statusPadre);
          let pesoHijo = getPesos(statusHijo);

          let finalPeso = pesoPadre + pesoHijo;
          let finalStatus = 'DOWN'
          if(finalPeso >= 0 && finalPeso <= 2) finalStatus = 'OK';
          if(finalPeso >= 3 && finalPeso <= 4) finalStatus = 'ALARM';
          if(finalPeso >= 5 && finalPeso <= 6) finalStatus = 'ALERT';
          if(finalPeso >= 7 && finalPeso <= 8) finalStatus = 'DOWN';

          let finalposition = [positionPadre,positionHijo]
          polilynes.value.push(optionsRect(finalStatus,finalposition));
      })
  })
  .catch(function (error) {
      showConsoleError(error);
  });
}

const showPolylines = async (isShow:boolean, isOnlyAlert:boolean) => {
  if(!isShow){
      finalPolilynes.value    = [];
      showPMPSM.value     = false;
  }
  else{
      let prePolylines:any[] = [];
      polilynes.value.forEach((polilyne:any)=> {
          if(isOnlyAlert){
              polilyne.colorLine.toUpperCase() !== 'GREEN' ? prePolylines.push(polilyne) : null     
          }else{
              prePolylines.push(polilyne)
              showPMPSM.value = true;
          }
      })
      finalPolilynes.value = prePolylines;
  }
}

const getLatLongRAJANT = async () => {
  await axios.get(CONSTAPIS.APIS.maps.getAllLatLngRAJ, { timeout : 10000 }).then((response)=>{
      elementRAJANT.value = response.data;

      elementRAJANT.value.forEach((element:EQUIPAMENTS) => {

          const latitud   = element.latitud;
          const longitud  = element.longitud; 
          let position    = [latitud,longitud]; 

          markersrajant.value.push({
              position    : position,
              label       : element.name,
              name        : element.name,
              ip          : element.ip,
              tipo        : element.tipo,
              status      : getStatusEquipment(element.ip??''),
          })
      })           
  })
  .catch(function (error) {
      showConsoleError(error);
  });
}

