<template>
    <v-sheet class="defaultCard" color="bgtopology">
        <span  class="titlePages">{{  t('analyticsPage.titlerx') }}</span>
        <v-row  class="ma-0" v-if="showPredict" >
            <v-col  
            class="my-3" 
            cols="12" sm="4" md="3" lg="2" xl="2"
            v-for="(item,i) in principalData" 
            :key="i"           
            >
                <CardTopology
                :status="item.status"
                :ip="item.ip"
                :title="item.name"
                :description="item.tipo" 
                @click="pressItem(item.ip,item.name)"           
                />
            </v-col>

            <!-- DIALOG -->
            <v-dialog
                v-model="dialog"
                width="85%"
                >
                <v-card class="card_dialog" color="background">
                    <v-row class="ma-0" justify="space-between" style="align-items: self-end !important;" >
                        <v-col cols="10">
                            <h4 v-if="xs"> {{ t('title.graphTitle')}} {{ titleDialog }}</h4>
                            <h2 v-else> {{ t('title.graphTitle')}} {{ titleDialog }}</h2>
                        </v-col>
                        <v-col cols="1">
                            <v-btn icon="mdi-close" variant="plain" @click="dialog = false"></v-btn>
                        </v-col>
                        <v-col v-if="valorTwo.length !== 0" cols="12" :style="xs ? 'text-align:start' :'text-align:end'" class="pr-16">
                            <span :class="xs ? 'datePredict' : ''">
                                {{ t('analyticsPage.titleDial1') }} [ {{  titleDate }} ] - {{ t('analyticsPage.titleDial2') }} {{  titleValue }}
                            </span>
                        </v-col>
                    </v-row>
                    <v-divider></v-divider>
                    <v-sheet class="defaultCard text-center"  color="bgtopology">
                        <CompareCharts
                        v-if="valorTwo.length > 0"
                        name-primary="Real" 
                        :color-first="CONSTCOLORS.COLOR_HTML.compare13"
                        :color-second="CONSTCOLORS.COLOR_HTML.compare14"
                        :name-secundary="t('labels.predict')" 
                        :title="t('analyticsPage.predvsreal1')"
                        :subtitle="t('analyticsPage.predvsreal2')"
                        :first-array="valorOne" 
                        :second-array="valorTwo" 
                        :typeChart="CONSTSTRINGS.STRINGS_HTML.predict" />
                        <Prog_circular v-else/>
                    </v-sheet>
                    <v-sheet class="defaultCard"  color="bgtopology">
                        <EasyDataTable
                        table-class-name="customize-table"
                        :headers="finalHeardes"
                        :items="items"
                        show-index
                        hide-footer
                        >
                        </EasyDataTable>
                    </v-sheet>    
                </v-card>
            </v-dialog>
        </v-row>
        <v-row v-else  class="ma-0 px-4 py-3">
            <h3> {{t('noInformation')}} </h3>
        </v-row>
    </v-sheet>  
</template>
<script setup lang="ts">
import { computed,  onMounted, ref, Ref, ComputedRef } from 'vue';
import axios                from 'axios';
import { useDisplay }       from 'vuetify';
import { useI18n }          from 'vue-i18n';

// Components 
import CONSTAPIS            from '@/constants/apis';
import CONSTCOLORS          from '@/constants/colors';
import CONSTSTRINGS         from '@/constants/strings';
import CardTopology         from '@/components/SmartLink/topology/CardTopology.vue';
import CompareCharts        from '@/components/SmartLink/charts/CompareCharts.vue';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';

// Interfaces
import { PREDICT_GRAPH }    from '@/interfaces/SmartLink/IA';
import { HEADER }           from '@/interfaces/SmartLink/Tables'
import { showConsoleError } from '@/functions/errors';

const { t }                         = useI18n();
const { xs }                        = useDisplay();
const projectName = process.env.VUE_APP_NAME_PROJECT;

/**
 * Components
 */     
const finalHeardes:ComputedRef<HEADER[]> = computed(() => [
    { text:  t('labels.date'),      value: "fecha",     sortable: false},
    { text:  t('labels.ip'),        value: "ip",        sortable: false},
    { text:  t('labels.cause'),     value: "causa",     sortable: false},
    { text:  t('labels.value'),     value: "detalle",   sortable: false},
])

/**
 * Mostrar el Dialogo
 */
const dialog                        = ref(false);

/**
 * Muestra el apartado de error, en vez de mostrar información o un espacio vacío. 
 * Generalmente es cuando desde la API no trae valores.
 */
const showPredict                   = ref(false);

/**
 * Son los parámetros que se envían a la API
 */
const params                        = ref({link : ''});

/**
 * Nombre del equipo seleccionado para mostrar su dialog
 */
 const titleDialog                   = ref('');

/**
 * Cuando le das click a algún item, captura su ip y su nombre
 * lo manda a la función "getPredict" mandando su parametros
 * all => para la tabla
 * pred/real => para el gráfico
 * muestra el dialog de los gráficos
 */
const pressItem = (ip:string, name:string ) => {
    dialog.value        = true;
    titleDialog.value   = name;
    params.value.link   = ip;
    getPredict(CONSTAPIS.APIS_STRINGS.all);
    getPredict(CONSTAPIS.APIS_STRINGS.real);
    getPredict(CONSTAPIS.APIS_STRINGS.pred);
}


/**
 * Valor principal de los predicts
 */
const principalData                 = ref();

/**
 * Captura todos los items tienen predicciones
 * traidos de la API: "getAllPredictRXLVLData"
 */
async function getAllPredRXData() {
    showPredict.value   = true;
    principalData.value = [];

    await axios.get(CONSTAPIS.APIS.predict.getAllPredRXData, { timeout: 10000 }).then((response) => {
        principalData.value = response.data;
        if(principalData.value.length > 0){
            showPredict.value   = true;
        }else{
            showPredict.value   = false;
        }
    })
    .catch(function (error) {
        showPredict.value = false;
        showConsoleError(error);
    });        
}


/**
 * Valor Real del gráfico
 */
 const valorOne:Ref<number[][]>      = ref([]);

/**
 * Valor Predict del Gráfico
 */
const valorTwo:Ref<number[][]>      = ref([]);

/**
 * Valor de la tabla
 */
const items:Ref<PREDICT_GRAPH[]>    = ref([]);

/**
 * Fecha del predict
 */
 const titleDate                     = ref('');

/**
 * Valor del predict
 */
const titleValue                    = ref('');

/**
 * Hace el pedido a la API según el tipo
 * @param type Tipo de Predicción de la API 
 */
async function getPredict(type:string) {
    if(type === CONSTAPIS.APIS_STRINGS.all)     items.value    = [];
    if(type === CONSTAPIS.APIS_STRINGS.real)    valorOne.value = [];
    if(type === CONSTAPIS.APIS_STRINGS.pred)    valorTwo.value = [];

    await axios.get(CONSTAPIS.APIS.predict[type],{params:params.value}).then((response) =>{

        /**
         * Los valoresd de la tabla
         */
        if(type === CONSTAPIS.APIS_STRINGS.all) {
            items.value             = response.data;
        }else{

            /**
             * Para los valores de predict y real
             */
            response.data.forEach((item:PREDICT_GRAPH)=>{
                let itemTime        = new Date(item.fecha);
                let milliseconds    = itemTime.getTime();

                // Para los valores REALES
                if(type === CONSTAPIS.APIS_STRINGS.real){ 

                    if(projectName === 'SmartLinkCananea'){
                        let prevValue = JSON.parse(item.kpi??`{'H' : 0, 'V' : 0, 'PER' : 0}`);
    
                        // Como trae valores simples o en un array, primero se valida
                        // Si existe y si es un objeto (un array), se captura el primer valor del array,
                        // Caso contrario se coloca el valor único que tiene
                        let RADIOrx   = (typeof prevValue['H']).toUpperCase() == 'OBJECT' && prevValue['H'] !== null ? prevValue['H'][1] : prevValue['H'];
                        
                        let values = RADIOrx
                        valorOne.value.push([milliseconds,values]);
                    }
                    else{
                        // Convierte a JSON la variable "kpi"
                        // Si no trae valores, convierte el valor por defecto: "{"tx": 0, "rx":0}"
                        let prevValue = JSON.parse(item.kpi??`{'tx': 0, 'rx': 0}`);
    
                        // Como trae valores simples o en un array, primero se valida
                        // Si existe y si es un objeto (un array), se captura el primer valor del array,
                        // Caso contrario se coloca el valor único que tiene
                        let RADIOrx   = (typeof prevValue['rx']).toUpperCase() == 'OBJECT' && prevValue['rx'] !== null ? prevValue['rx'][0] : prevValue['rx'];
                        
                        let values = RADIOrx
                        valorOne.value.push([milliseconds,values]); 
                    }
                }

                // Para los valores de predict
                else{
                    let values = parseFloat(item.kpi);
                    valorTwo.value.push([milliseconds,values]);
                }                           
            })
            
            /**
             * Capturas la fecha y el valor del último predict
             */
            if(type === CONSTAPIS.APIS_STRINGS.pred){
                let fecha           =  new Date(valorTwo.value.slice(-1)[0][0]);
                titleDate.value     = fecha.toLocaleString("es-PE");
                titleValue.value    = (valorTwo.value.slice(-1)[0][1]).toString();
            }
        } 
    });         
}

onMounted(()=>{
    getAllPredRXData();
})

</script>

<style>
.datePredict{
    font-size: 13px;
}
</style>