<template>
    <div class="mx-4">
        <v-sheet color="transparent">
            <v-sheet v-if="showwlan0" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="my-4" color="bgtopology">
                <SingleChart v-if="arraywlan0noise.length !== 0"  :name="'wlan0noise'" :first-array="arraywlan0noise" :title=" t('title.noiseLevel') +  ` < Wlan0 >`" :color="CONSTCOLOR.COLOR_HTML.single"/>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
            <v-sheet v-if="showwlan1" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="my-4" color="bgtopology">
                <SingleChart v-if="arraywlan1noise.length !== 0"  :name="'wlan1noise'" :first-array="arraywlan1noise" :title=" t('title.noiseLevel') + ` < Wlan1 > `" :color="CONSTCOLOR.COLOR_HTML.single"/>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
            <v-sheet v-if="showwlan2" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="my-4" color="bgtopology">
                <SingleChart v-if="arraywlan2noise.length !== 0"  :name="'wlan2noise'" :first-array="arraywlan2noise" :title=" t('title.noiseLevel') + ` < Wlan2 > `" :color="CONSTCOLOR.COLOR_HTML.single"/>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
            <v-sheet v-if="showwlan3" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="my-4" color="bgtopology">
                <SingleChart v-if="arraywlan3noise.length !== 0"  :name="'wlan3noise'" :first-array="arraywlan3noise" :title=" t('title.noiseLevel') + ` < Wlan3 > `" :color="CONSTCOLOR.COLOR_HTML.single"/>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
        </v-sheet>
        
    </div>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref } from 'vue'
import axios                from 'axios';
import { useDisplay }       from 'vuetify';
import { useI18n } from 'vue-i18n';

// COMPONENTS
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleChart          from '@/components/SmartLink/charts/SingleChart.vue';

// CONSTANTS
import CONSTAPIS            from '@/constants/apis';
import CONSTCOLOR           from '@/constants/colors';

// INTERFACES
import { DEFAULT_GRAPH }    from '@/interfaces/SmartLink/Graphs'

import { showConsoleError } from '@/functions/errors';

const props = defineProps({
    ip      : { required:true, type:String },
    title   : { required:true, type:String },
})

const { xs } = useDisplay();

/**
 * Component
 */
const { t }             = useI18n()
const arraywlan0noise:Ref<number[][]>   = ref([]);
const arraywlan1noise:Ref<number[][]>   = ref([]);
const arraywlan2noise:Ref<number[][]>   = ref([]);
const arraywlan3noise:Ref<number[][]>   = ref([]);
const showwlan0         = ref(true);
const showwlan1         = ref(true);
const showwlan2         = ref(true);
const showwlan3         = ref(true);


/**
 * Capturamos los valores de la API: "getGraphTopologyData".
 * Envía la IP y el tipo de petición:
 * Este caso sería "wlan"
 * @param type 
 */
const getDataGraph = (type:string) =>{

    const params = {
        ip      : props.ip,
        type    : type
    }

    axios.get(CONSTAPIS.APIS.topology.getGraphTopo,{ params : params, timeout : 10000 }).then((item) => {

        if(item.data.length === 0) {
            hideAll();
        }
        else{
            item.data.forEach((element:DEFAULT_GRAPH)=>{
                let tiempo          = new Date(element.fecha ? element.fecha : '');
                let milliseconds    = tiempo.getTime();
                
                // Convierte a JSON la variable "kpiObj"
                // Si no existe, el valor por default es ""
                let getItems        = JSON.parse(element.kpiObj??'')

                if( getItems.wlan0 ) { arraywlan0noise.value.push([milliseconds,(getItems.wlan0.rxBytes??0)/(1024 * 1024)]) }
                if( getItems.wlan1 ) { arraywlan1noise.value.push([milliseconds,(getItems.wlan1.rxBytes??0)/(1024 * 1024)]) }
                if( getItems.wlan2 ) { arraywlan2noise.value.push([milliseconds,(getItems.wlan2.rxBytes??0)/(1024 * 1024)]) }
                if( getItems.wlan3 ) { arraywlan3noise.value.push([milliseconds,(getItems.wlan3.rxBytes??0)/(1024 * 1024)]) }
                
            })
            arraywlan0noise.value.length === 0 ? showwlan0.value = false : showwlan0.value = true;
            arraywlan1noise.value.length === 0 ? showwlan1.value = false : showwlan1.value = true;
            arraywlan2noise.value.length === 0 ? showwlan2.value = false : showwlan2.value = true;
            arraywlan3noise.value.length === 0 ? showwlan3.value = false : showwlan3.value = true;
        }
    })
    .catch(function (error) {
        hideAll();
        showConsoleError(error);
    });
}

const hideAll = () => {
    showwlan0.value = false;
    showwlan1.value = false;
    showwlan2.value = false;
    showwlan3.value = false;
}

onMounted(() => {
    getDataGraph(CONSTAPIS.APIS_STRINGS.wlan);
})

</script>