const handleFileRajantUpload = (event:any) => {

    if(event.target.files[0] !== undefined){
        fileItemRajant.value = [];
        const file = event.target.files[0];
        const reader = new FileReader();

        reader.onload = (e:any) => {
            const data          = new Uint8Array(e.target.result);
            const workbook      = read(data, { type: 'array' });
            const worksheet     = workbook.Sheets[workbook.SheetNames[0]];
            const jsonData      = utils.sheet_to_json(worksheet, { header: 1 });
            jsonData.forEach((elem:any) => {
                if(elem[3]?.split('.')[0] === '10'){
                    let ipfinal     = elem[3];
                    let hostname    = elem[1];  
                    let tipo        = TYPE_EQUIP[elem[6].trim()]||elem[6];       
                    let subtipo     = SUBTYPE_EQUIP[elem[6].trim()]||elem[6]       
                    let isMatch     = false;
                    let isMatchName = false;
                    allEquipment.value.find(({ip,name}:IP_EQUIPAMENT)=>{
                        if(name == hostname){ isMatchName = true }
                        if(ip == ipfinal)   { isMatch = true }
                    })
                    if(isMatch){
                        let objFile = {
                            ip          :   ipfinal,
                            hostname    :   hostname,
                        }
                        fileItemRajant.value.push(objFile);
                    }
                    else{
                        if(isMatchName && !isMatch){
                            let objFileName = {
                                ip              :   ipfinal,
                                hostname        :   hostname,
                                tipo            :   tipo,
                                subtipo         :   subtipo,
                            }
                            fileItemNameRajant.value.push(objFileName);
                        } else{
                            let objFile:GENERAL_EQUIPAMENT = {
                                elemid          :   0,
                                ip              :   ipfinal,
                                hostname        :   hostname,
                                marca           :   'Rajant',
