                }
            })
            if(fileItemRajant.value.length === 0) snackbar.value = true;
        };
        reader.readAsArrayBuffer(file);
    }else{
        fileItemRajant.value = [];
    }
}

const handleFileCambiumUpload = (event:any) => {
    
    if(event.target.files[0] !== undefined){
        fileItemCambium.value = [];

        const file = event.target.files[0];
        const reader = new FileReader();

        reader.onload = (e:any) => {

            const data      = new Uint8Array(e.target.result);
            const workbook  = read(data, { type: 'array' });
            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
            const jsonData  = utils.sheet_to_json(worksheet, { header: 1 });

            jsonData.forEach((elem:any) => {

                if(elem[5]?.split('.')[0] === '10' && typeof elem[5] === 'string' && elem[5] !== ''){

                    let ipfinal     = elem[5];
                    let hostname    = elem[4];
                    let modelo      = elem[7];
                    let serial      = elem[8];
                    let isMatch     = false;
                    let isMatchName = false;

                    allEquipment.value.find(({ip,name}:IP_EQUIPAMENT)=>{
                        if(ip == ipfinal){
                            isMatch = true;
                        }
                        if(name == hostname){
                            isMatchName = true;
                        }
                    })

                    if(isMatch){
                        let objFile = {
                            ip          :   ipfinal,
                            hostname    :   hostname,
                            modelo      :   modelo,
                            serial      :   serial
                        }
                        fileItemCambium.value.push(objFile);
                    } else{
                        if(isMatchName && !isMatch){
                            let objFileName = {
                                ip              :   ipfinal,
                                hostname        :   hostname,
                                modelo          :   modelo,
                                serial          :   serial,
                                tipo            :   modelo.split(' ').slice(-1) == 'SM' ? 'PMP' : 'PMP-AP',
                            }
                            fileItemNameCambium.value.push(objFileName);
                        } else{
                            let objFile:GENERAL_EQUIPAMENT = {
                                elemid      :   0,
                                ip          :   ipfinal,
                                hostname    :   hostname,
                                modelo      :   modelo,
                                serial      :   serial,
                                marca       :   'Cambium',
                                tipo        :   modelo.split(' ').slice(-1) == 'SM' ? 'PMP' : 'PMP-AP',
                                subtipo     :   'Distribucion',
                                observacion :   ' ',
                                ubicacion   :   ' ',
                                padre       :   0,
                                gpsLat      :   ' ',
                                gpsLong     :   ' ',
                                gpsAlt      :   ' '
                            }
                            fileItemInsertCambium.value.push(objFile);
                        }
                    }

                    let objFile = {
                        ip          :   ipfinal,
                        hostname    :   hostname,
                        modelo      :   elem[7],
                        serial      :   elem[8]
                    }
                    fileItemCambium.value.push(objFile);
                }
            })

            if(fileItemCambium.value.length === 0) snackbar.value = true;
        };
        reader.readAsArrayBuffer(file);
    }else{
        fileItemCambium.value = [];
    }
}

const clearData = (type:string) => {
    if(type === 'rajant') fileItemRajant.value = []
    if(type === 'cambium') fileItemCambium.value = []
}


const rajantEvent = async () => {
    await axios.put(CONSTAPIS.APIS.csv.editDataInven,fileItemRajant.value).then((response)=>{
        if(response.status === 200){
            rajant.value                = [];
            fileItemRajant.value        = [];
        }                 
    })

    await axios.put(CONSTAPIS.APIS.csv.editDataByName,fileItemNameRajant.value).then((response)=>{
        if(response.status === 200){
            rajant.value                = [];
            fileItemNameRajant.value    = [];
        }                 
    })

    await axios.post(CONSTAPIS.APIS.csv.insertDataInven,fileItemInsertRajant.value).then((response)=>{
        if(response.status === 200){
            rajant.value                  = [];
            fileItemInsertRajant.value    = [];
        }                 
    })
}

const cambiumEvent = async () => {
    await axios.put(CONSTAPIS.APIS.csv.editDataInven,fileItemCambium.value).then((response)=>{
        if(response.status === 200){
            cambium.value               = [];
            fileItemCambium.value       = [];
        } 
    })

    await axios.put(CONSTAPIS.APIS.csv.editDataByName,fileItemNameCambium.value).then((response)=>{
        if(response.status === 200){
            cambium.value               = [];
            fileItemNameCambium.value   = [];
        } 
    })
    
    await axios.post(CONSTAPIS.APIS.csv.insertDataInven,fileItemInsertCambium.value).then((response)=>{
        if(response.status === 200){
            cambium.value               = [];
            fileItemInsertCambium.value = [];
        }                 
    })
}

onMounted(async() => {
    await axios.get(CONSTAPIS.APIS.csv.getDataInven).then((res)=>{
        allEquipment.value = res.data;
    })
})


