<template>
    <EasyDataTable
    show-index
    table-class-name="customize-table"
    :headers="headers"
    :items="items"
    :rows-per-page="8"
    :hide-footer="false"
    @click-row="postURL"
    class="ma-4"
    >         
    </EasyDataTable>
</template>

<script setup lang="ts">

import { /*computed,*/ ref, Ref, onMounted }       from 'vue';
// import { useI18n }                  from 'vue-i18n';
import axios                        from 'axios';
import { HEADER, ALLHEADER }        from '@/interfaces/SmartLink/Tables';
import { EQUIPAMENTS } from '@/interfaces/SmartLink/Equips';
// import { CONFIGURATION }            from '@/interfaces/GestorHCGLINK/Equips';
import CONSTAPIS                 from '@/constants/apis'; 
import { showConsoleError } from '@/functions/errors';

// const { t }                     = useI18n();
const headers:Ref<HEADER[]>     = ref([]);
const items: Ref<EQUIPAMENTS[]> = ref([]);
const HEADERS__PRIMARY_TABLE:ALLHEADER = {
    // computed(() => t('labels.date'))
    header : [
        { text: 'IP Address',   value: "ip",            sortable: true },
        { text: 'Nombre',       value: "name",          sortable: true },
        { text: 'Tipo',         value: "tipo",          sortable: true },
        { text: 'Subitpo',      value: "subtipo",       sortable: true },
        { text: 'Mark',         value: "marca",         sortable: true },
    ]
};
headers.value = HEADERS__PRIMARY_TABLE['header'];

const getItems = async() => {
    await axios.get(CONSTAPIS.APIS.lte.getIPLTE, { timeout : 10000 }).then((response) =>{
        items.value = response.data;
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const postURL = (item: EQUIPAMENTS) => {
    window.open(`http://${item.ip}/webfig/#interfaces`, "_blank", "noopener,noreferrer");
}

onMounted(() => {
    getItems()  
})
</script>