    header : [
        { text: 'IP Address',   value: "ip",            sortable: true },
        { text: 'Nombre',       value: "name",          sortable: true },
        { text: 'Tipo',         value: "tipo",          sortable: true },
        { text: 'Subitpo',      value: "subtipo",       sortable: true },
        { text: 'Mark',         value: "marca",         sortable: true },
    ]
};
headers.value = HEADERS__PRIMARY_TABLE['header'];

const sendData:Ref<ENLACES[]> = ref([])



const finalInfoData:Ref<LASTENLACEINFO> = ref({})
const finalInfoData30Days:Ref<ALLDATAINFO> = ref({});
const selectedIP        = ref('');
const selectedNAME      = ref('');
const arrayDates:Ref<string[]>   = ref([]);
// const showDataTable     = ref(false);

const getMaxDatesLTE = async() => {
    await axios.get(CONSTAPIS.APIS.gestor.getMaxDatesLTE,{ timeout: 10000 }).then((response) => {
        let previousArray = response.data;
        if(previousArray.length > 0) {
            previousArray.forEach((item:GENERAL_EQUIPAMENT) => {
                if(item.fecha) arrayDates.value.push(item.fecha);
            })
        }
        else{ console.log('no hay datos')}        
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}
const getLastDatesLTE = async() => {
    let params = {
        dates  : arrayDates.value
    }    
    sendData.value = [];
    await axios.get(CONSTAPIS.APIS.gestor.getLastLTE,{ params:params, timeout: 10000 }).then((response) => {
        let previousArray = response.data;
        if(previousArray.length > 0) {
            let enlaceByName:ENLACEBYNAME    = {}
            previousArray.forEach((item:DATABASEINFO) => {
                let enlaces             = JSON.parse(item.enlaces);
                if(enlaces.length > 0) {
                    if(!finalInfoData.value[item.ip]){
                        finalInfoData.value[item.ip] = [];

                        enlaces.forEach((enlace:ENLACES) => {
                            enlaceByName[enlace.ifDescr] = {
                                [item.ip] : {
                                    RXBytes : parseInt(enlace.RXBytes),
                                    TXBytes : parseInt(enlace.TXBytes)
                                }
                            }
                        })
                    }else{
                        let finalEnlaces:ENLACES[] = [];
                        enlaces.forEach((enlace:ENLACES) => {
                            let prevObj:ENLACES = {
                                ifDescr : enlace.ifDescr.split("\"")[1].toUpperCase(),
                                ifMtu   : enlace.ifMtu,
                                ifSpeed : enlace.ifSpeed,
                                RXBytes : ((parseInt(enlace.RXBytes) - enlaceByName[enlace.ifDescr][item.ip].RXBytes)/1024).toFixed(2),
                                TXBytes : ((parseInt(enlace.TXBytes) - enlaceByName[enlace.ifDescr][item.ip].TXBytes)/1024).toFixed(2),
                                ip      : enlace.ip,
                            }
                            finalEnlaces.push(prevObj)
                        })
                        finalInfoData.value[item.ip] = finalEnlaces;
                        // enlaceByName = {};
                    }
                } 
                
            })
        }       
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const getAllDaysLTE = async() => {
    axios.get(CONSTAPIS.APIS.gestor.getLast30DaysLTE,{ timeout : 10000 }).then((response) => {
        let previousArray = response.data;
        if(previousArray.length > 0) {
            previousArray.forEach((item:DATABASEINFO) => {
                let enlaces = JSON.parse(item.enlaces);
                if(enlaces.length > 0){
                    if (!finalInfoData30Days.value[item.ip]) {
                        finalInfoData30Days.value[item.ip] = {};
                    }
                    enlaces.forEach((enlace:ENLACES) => {
                        const interfaceName = enlace.ifDescr?.split("\"")[1].toUpperCase()??'item';
                        if (!finalInfoData30Days.value[item.ip][interfaceName]) {
                            finalInfoData30Days.value[item.ip][interfaceName] = {
                                rxbytes: [],
                                txbytes: []
                            };
                        }
                        let tiempo          = new Date(item.fecha);
                        let milliseconds    = tiempo.getTime();
                            finalInfoData30Days.value[item.ip][interfaceName].rxbytes.push([milliseconds, parseInt(enlace.RXBytes)]);
                            finalInfoData30Days.value[item.ip][interfaceName].txbytes.push([milliseconds, parseInt(enlace.TXBytes)]);
                    });
                } 
            })
        }
        else { console.log('no hay datos') }
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const showDataInfo = (item:TABLEINFO) => {
    sendData.value = [];
    sendData.value = finalInfoData.value[item.ip] ? finalInfoData.value[item.ip] : [] ;
    selectedIP.value = item.ip;
}

const finalRXBYTES:Ref<number[][]> = ref([]);
const finalTXBYTES:Ref<number[][]> = ref([]);
const dialog       = ref(false);

const showDialog = (name:string) => {
    let selectedInfo = finalInfoData30Days.value[selectedIP.value][name]
    selectedNAME.value = name
    finalRXBYTES.value = selectedInfo.rxbytes;
    finalTXBYTES.value = selectedInfo.txbytes;
    dialog.value = true;
}

const closeDialog = () => {
    dialog.value = false;
}

const getItems = async() => {
    await axios.get(CONSTAPIS.APIS.lte.getIPLTE,{ timeout : 10000 }).then((response) =>{
        items.value = response.data;
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

onMounted(async () => {
    getItems() 
    await getAllDaysLTE();
    await getMaxDatesLTE();
    await getLastDatesLTE();
    // setTimeout(()=>{
    // }, 1000)
})


