<template> 
<v-sheet color="transparent" class="containerAreaSheet"  v-click-outside="{ handler: onClickOutside }" @click="activeClick" >
    <v-sheet color="transparent" >
            <AreaChart 
            v-if="!isLoadingData" 
            :array1="finalOKArray"
            :array2="finalALEArray"
            :array3="finalALAArray"
            :array4="finalDOWArray"
            :isactive="isactive"
            />     
    </v-sheet>
    <v-sheet v-if="!isactive" class="areaChartSheet" ></v-sheet>

</v-sheet>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref, computed } from 'vue';

// COMPONENTS
import AreaChart        from '@/components/SmartLink/charts/AreaChart.vue';

// INTERFACES
import { FinalStatus }  from '@/interfaces/SmartLink/Equips'

const props = defineProps({
    arrayItems: { type: Array<FinalStatus>, required:true }
})

const arrayFinal                        = computed(() => props.arrayItems);
const finalOKArray:Ref<number[][]>      = ref([]);
const finalALEArray:Ref<number[][]>     = ref([]);
const finalALAArray:Ref<number[][]>     = ref([]);
const finalDOWArray:Ref<number[][]>     = ref([]);

/**
 * Captura los datos obtenidos por el PROPS
 * y los almacena en los array correspondientes
 * con sus tiempos
 */
const getAllInformation = () => {
    arrayFinal.value.forEach((status:FinalStatus)=>{
        const { ok, alert, alarm, down, fecha} = status
        let tiempo          = new Date(fecha);
        let milliseconds    = tiempo.getTime();
        finalOKArray.value.push([milliseconds,ok]);
        finalALEArray.value.push([milliseconds,alert]);
        finalALAArray.value.push([milliseconds,alarm]);
        finalDOWArray.value.push([milliseconds,down]);
    })
    getAllData();
}


/**
 * Tiempo de Delay para que cambie de valor de true a false la variable "isLoadingData"
 */
const timeStop                          = ref(300);

/**
 * Para poder visualizar los gráficos porque si se carga sin esperar nada, mostrará vacío el gráfico
 */
const isLoadingData                     = ref(true);

/**
 * Ordena los valores (no recuerdo porqué usé este sort, pero debería de venir ordenado desde la API)
 */
const getAllData = () =>{
    finalOKArray.value.sort()
    finalALEArray.value.sort()
    finalALAArray.value.sort()
    finalDOWArray.value.sort()
    isLoadingData.value         = true;
    setTimeout(()=>{
        isLoadingData.value     = false;
    },timeStop.value)
}


/**************************************
****** ACTIVAR/DESACTIVAR SCROLL ******
**************************************/

// Se colocó un elemento delante del gráfico para que cuando se pase el mouse por encima, se evite
// hacer el scroll dentro del gráfico, con el fin de evitar inconvenientes para el usuario final

// variable de la activación/desactivación
const isactive = ref(false);


/**
 * Desactiva el scroll dentro de gráfico
 */
const onClickOutside = () => {
    isactive.value = false;
}

/**
 * Activa el scroll dentro del gráfico
 */
const activeClick = () =>{
    isactive.value = true;
}

onMounted(()=>{
    getAllInformation()
})

</script>

<style>
.v-btn-toggle > .v-btn.v-btn--active:not(.v-btn--disabled) > .v-btn__overlay {
    opacity: .4 !important;
}
.v-btn-group--density-default.v-btn-group {
    height: 35px !important;
}
.containerAreaSheet{
    position:relative;
    z-index: 3;
}
.areaChartSheet{
    position:absolute;
    top: 0%;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    z-index: 2;
    background-color: rgba(0,0,0,0.05);
}
.areaChartSheet:hover{
    border: 2px solid rgba(255,255,255,0.5);
}
</style>