const postPORTItems = (tipo:string, latitud:number, longitud:number, color:string, valor:string, index:number, fecha:string) => {
    let finalITEM = {                        
            "type": "Feature",
            "properties": {
                "level": 0,
                "name": `AllGraph${tipo}`,
                "height": 40,
                "base_height": 0,
                "color": color,
                "center": [longitud,latitud],
                "valor": valor,
                "fecha" : fecha
            },
            "geometry": {
                "coordinates": [ getFinalPositions(latitud,longitud) ],
                "type": "Polygon"
            },
            'id': index 
    }
    if(tipo.toUpperCase() === 'RSRP')       { finalRSRPArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'RSRQ')       { finalRSRQArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'RSSI')       { finalRSSIArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'SNR')        { finalSNRArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'LATENCIA')   { finalLATENCIAArray.value.push(finalITEM) }
}

const setSourceAndLayer = async (tipo:string, source:string, alldata:any, layer:string) => {
    const mySource = map.getSource(source);
    if(mySource === undefined){ 
        map.addSource(source, { 'type': 'geojson', 'data': alldata});        
        map.addLayer({
            'id': layer,
            'type': 'fill-extrusion',
            'source': source,
            'paint': {
                'fill-extrusion-color': ['get', 'color'],
                'fill-extrusion-height': ['get', 'height'],
                'fill-extrusion-base': ['get', 'base_height'],
                'fill-extrusion-opacity':1,
            }
        });
        
        const popup = new maplibregl.Popup({
            closeButton: false,
            closeOnClick: false
        });

        map.on('mouseenter', layer, (e:any) => {
            map.getCanvas().style.cursor = 'pointer';
            
            const coordinates:any = [e.lngLat.lng, e.lngLat.lat];
            const valor = e.features[0].properties.valor ;
            let mensaje = `${valor}` 

            while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
            }

            popup.setLngLat(coordinates).setHTML(mensaje).addTo(map);
        });

        map.on('mouseleave', layer, () => {
            map.getCanvas().style.cursor = '';
            popup.remove();
        });
    }
    else{
        mySource.setData(alldata)
    }
}

const showCircles = async (firstDate:string, lastDate:string) => {
    circles.value = [];
    allCirclesFiltered.value.forEach((circle:CIRCLE) => {
        if(circle.fecha && new Date(circle.fecha).getTime() <= new Date(lastDate).getTime() && new Date(circle.fecha).getTime() >= new Date(firstDate).getTime()){
            circles.value.push(circle)
        }
    })
    showRangeSlider.value = true;
}

watch(modelSliderStatus,(a:number[], b:number[])=>{
    if(JSON.stringify(a)!==JSON.stringify(b)) {
        showCircles(arrayDatesCircle.value[a[0]],arrayDatesCircle.value[a[1]]);
        indexFrom.value = a[0];
        indexTo.value   = a[1];
    }
})

const seasonStatus = (val:number) => {
    let finalDate = arrayDatesCircle.value[val];
    return finalDate;
}

watch(getValueStoreDRIVE,(a:string, b:string)=>{
    a !== b ? resetData() : null;
    finalListTruck.value = haulTrucksList.value;
})

const clearLayerSource = (layer:string, source:string) => {
    if(map.getLayer(layer)) { map.removeLayer(layer); }
    if(map.getSource(source)) { map.removeSource(source); }
}

const watchLayer = (selectedVar: Ref<boolean>, type: string, source: string, finalObject: Ref<any>, layer: string) => {
    watch(selectedVar, (state) => {
        if (state) {
            setSourceAndLayer(type, source, finalObject.value, layer);
        } else {
            clearLayerSource(layer, source);
        }
    });
}; 

watchLayer(selectedRSRP, 'rsrp', 'SourceRSRP', finalRSRPObject, 'LayerRSRP');
watchLayer(selectedRSRQ, 'rsrq', 'SourceRSRQ', finalRSRQObject, 'LayerRSRQ');
watchLayer(selectedRSSI, 'rssi', 'SourceRSSI', finalRSSIObject, 'LayerRSSI');
watchLayer(selectedSNR, 'snr', 'SourceSNR', finalSNRObject, 'LayerSNR');
watchLayer(selectedLATENCIA, 'latencia', 'SourceLATENCIA', finalLATENCIAObject, 'LayerLATENCIA');

const resetData = () => {
    circles.value = [];
    arrayDatesCircle.value = [];
    haulTruckSelected.value = '';
    infoWindowVisible.value = false;
}
const color:STRINGS = {
    '#E04637' : '#lineStyleRed',
    '#F9DC4B' : '#lineStyleYellow',
    '#427739' : '#lineStyleGreen',
    '#CAC8C8' : '#lineStyleGrey',
    '#00A5E8' : '#lineStyleBlue',
}

const getAllCoordinates = (finalObject: Ref<any>) => {
    let prevCoordinates = '';
    finalObject.value.features.forEach((element:any) => {
        const finalCoord1 = (element.geometry.coordinates[0][0]);
        const finalCoord2 = (element.geometry.coordinates[0][1]);
        const finalCoord3 = (element.geometry.coordinates[0][2]);
        const finalCoord4 = (element.geometry.coordinates[0][3]);
        const finalCoord5 = (element.geometry.coordinates[0][4]);
        let someCoordinates = `
            <Placemark>
                <name>${element.id}</name>
                <description> 
                    <b>Detalles: </b> <br/>
                    Posicion: ${element.properties.center}<br/>
                    Valor:${element.properties.valor}.<br/>
                    Fecha:${element.properties.fecha}.
                </description>
                <styleUrl>${color[element.properties.color??'#CAC8C8']}</styleUrl>
                <MultiGeometry>
                    <Polygon>
                        <extrude>1</extrude>
                        <altitudeMode>relativeToGround</altitudeMode>
                        <outerBoundaryIs>
                            <LinearRing>
                            <coordinates>
                                ${finalCoord1[0]},${finalCoord1[1]},0
                                ${finalCoord2[0]},${finalCoord2[1]},0
                                ${finalCoord3[0]},${finalCoord3[1]},0
                                ${finalCoord4[0]},${finalCoord4[1]},0
                                ${finalCoord5[0]},${finalCoord5[1]},0
                            </coordinates>
                            </LinearRing>
                        </outerBoundaryIs>
                    </Polygon>
                    <Polygon>
                        <extrude>1</extrude>
                        <altitudeMode>relativeToGround</altitudeMode>
                        <outerBoundaryIs>
                            <LinearRing>
                            <coordinates>
                                ${finalCoord1[0]},${finalCoord1[1]},20
                                ${finalCoord2[0]},${finalCoord2[1]},20
                                ${finalCoord3[0]},${finalCoord3[1]},20
                                ${finalCoord4[0]},${finalCoord4[1]},20
                                ${finalCoord5[0]},${finalCoord5[1]},20
                            </coordinates>
                            </LinearRing>
                        </outerBoundaryIs>
                    </Polygon>
                </MultiGeometry>
            </Placemark>
        `
        prevCoordinates = prevCoordinates + someCoordinates
    })

    return prevCoordinates
}

const getFolderItems = (title:string,finalCoords:string) => {
    let folder =  `
        <Folder> 
            <name> ${title} </name>
            ${finalCoords}
        </Folder>
    `
    return folder
}

const getKMZ = async () => {

    
 // Contenido del archivo KML
 let kmlContent = `<?xml version="1.0" encoding="UTF-8"?>
  <kml xmlns="http://www.opengis.net/kml/2.2">
    <Document>
      <name>EQUIPO : ${haulTruckSelected.value}</name>

    <Style id="lineStyleRed">
      <LineStyle>
        <color>ff00ffff</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ffff</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleYellow">
      <LineStyle>
        <color>ff00ffff</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ffff</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleGreen">
      <LineStyle>
        <color>ff00ff00</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ff00</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleGrey">
      <LineStyle>
        <color>ff808080</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff808080</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleBlue">
      <LineStyle>
        <color>ffff0000</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ffff0000</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>
    `
    const finalRSRPCoords       = getAllCoordinates(finalRSRPObject)
    const finalRSRQCoords       = getAllCoordinates(finalRSRQObject)
    const finalRSSICoords       = getAllCoordinates(finalRSSIObject)
    const finalSNRCoords        = getAllCoordinates(finalSNRObject)
    const finalLATENCIACoords   = getAllCoordinates(finalLATENCIAObject)

    let finalFolderRSRP         = getFolderItems('RSRP',finalRSRPCoords)
    let finalFolderRSRQ         = getFolderItems('RSRQ',finalRSRQCoords)
    let finalFolderRSSI         = getFolderItems('RSSI',finalRSSICoords)
    let finalFolderSNR          = getFolderItems('SNR',finalSNRCoords)
    let finalFolderLATENCIA     = getFolderItems('LATENCIA',finalLATENCIACoords)
      
  let finalKmlConent = `
   </Document>
  </kml>
  `

  kmlContent = kmlContent + finalFolderRSRP + finalFolderRSRQ + finalFolderRSSI + finalFolderSNR + finalFolderLATENCIA + finalKmlConent;

  // Crear un Blob con el contenido del KML
  const blob = new Blob([kmlContent], { type: "application/vnd.google-earth.kml+xml" });
  const url = URL.createObjectURL(blob);

  // Crear un enlace y simular el clic para descargar
  const link = document.createElement("a");
  link.href = url;
  link.download = `KML-EQUIPO: ${haulTruckSelected.value}.kml`;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}

let map:any = '' ;

onMounted(async() => {    
    
    map  = new maplibregl.Map({
        container: 'map',
        center: [-110.34928166, 30.972732],
        zoom: 14,
        pitch: 40,
        bearing: 30,
        antialias: true,
        style: 'https://api.maptiler.com/maps/satellite/style.json?key=6JWVt6LFaY22nquimZpF',
    });
    
    map.addControl(new maplibregl.NavigationControl());

    getAllHaulTrucks();
})


