                  fecha   : item.fecha??'',
                  position: [lat,lng],
                  ip      : haulTruckSelected.value,
                  tipo    : 'Haultruck',
                  img     : preImg,
                  color   : color 
              })
          })
      }
      else {
          showError.value = true;
          showFiltersStatus.value = false;
          disableSearchTruck.value = true
      }
  })
  .catch(function (error) {
      showConsoleError(error);
  });
}


watch(modelSliderStatus,(a:number[], b:number[])=>{
  if(JSON.stringify(a)!==JSON.stringify(b)) showCircles(arrayDatesCircle.value[a[0]],arrayDatesCircle.value[b[1]])
})

const showCircles = async (firstDate:string, lastDate:string) => {
  circles.value = [];
  allCirclesFiltered.value.forEach((circle:CIRCLE) => {
      if(circle.fecha && new Date(circle.fecha).getTime() <= new Date(lastDate).getTime() && new Date(circle.fecha).getTime() >= new Date(firstDate).getTime()){
          circles.value.push(circle)
      }
  })
}

const getColorByLatency = (latency:number) => {
  let finalcolor = '';
  if(latency === -1)                      finalcolor = 'grey';
  if(latency >= 0     && latency <= 100)  finalcolor = 'green';
  if(latency >= 100   && latency <= 200)  finalcolor = 'yellow';
  if(latency >= 200   && latency <= 500)  finalcolor = 'red';
  return finalcolor
}

/**
* When click in Item, show Dialog Component
*/
const showDialog = (ip:string, type:string, name:string, status:string) =>{
  dialog.value            = !dialog.value;
  selectedName.value  = name;
  selectedIp.value        = ip;
  selectedType.value      = type;
  finalStatus.value       = status;
}

const closeDialog = (value:boolean) => {
  dialog.value            = value
}

const latAlarm:Ref<any>              = ref([]);
const latAlert:Ref<any>              = ref([]);
const totalClusteringData:Ref<CLUSTERING[]> = ref([]);
const valueSliderMap                        = ref(12);
const arrayDatesClustering:Ref<string[]>    = ref([])


const get12HoursByClustering = async () => {
  await axios.get(CONSTAPIS.APIS.ia.get12HourCluster, { timeout : 10000 }).then((resp) => {
      let arrayFechas = resp.data;
      arrayFechas.forEach((item:CLUSTERING) => {
          arrayDatesClustering.value.push(item.fecha??'01/01/1900') 
      })
      arrayDatesClustering.value.sort()
  }) 
  .catch(function (error) {
      showConsoleError(error);
  });
}

const getClusteringMap = async () => {

  let params = {
      fecha : arrayDatesClustering.value[0]
  }

  await axios.get(CONSTAPIS.APIS.ia.getClusterData,{ params : params, timeout : 10000}).then((resp) => {
      totalClusteringData.value = resp.data;
      
      if(totalClusteringData.value.length > 0){
          getFilterByHours(arrayDatesClustering.value[arrayDatesClustering.value.length - 1]);
          showClusterChBox.value = false;
      }
      else{
          showClusterChBox.value = true;
      }
  })
  .catch(function (error) {
      showConsoleError(error);
  });
}

const getFilterByHours = (fechas:string) => {
  let arrayTotal          = totalClusteringData.value;
  let finalArray:CLUSTERING[]    = [];
  arrayTotal.forEach((element:CLUSTERING)=>{
      if(element.fecha && new Date(element.fecha).getTime() == new Date(fechas).getTime()){
          finalArray.push(element)
      }
  })
  getClusteringByHours(finalArray);
}

const getClusteringByHours = (arrayProv:CLUSTERING[]) => {
  latAlarm.value               = [];
  latAlert.value               = [];
  let princAlert:CLUSTERING[]  = [];
  let princAlarm:CLUSTERING[]  = [];

  arrayProv.forEach((item:CLUSTERING) => {
      if(item.label === 3) {
          princAlert.push(item)
          let latitud         = item.latitud??0;
          let longitud        = item.longitud??0;
          latAlert.value.push({
              position: [latitud, longitud]
          })
      }
      if(item.label === 4 ) {
          princAlarm.push(item)
          let latitud         = item.latitud??0;
          let longitud        = item.longitud??0;
          latAlarm.value.push({
              position: [latitud, longitud]
          })
      }
  })
}

const season = (val:number) => {
  let finalDate = arrayDatesClustering.value[val-1];
  return finalDate;
}

const addHours = () =>{
  if( valueSliderMap.value < 12) valueSliderMap.value ++;
}

const deleteHours = () =>{
  if( valueSliderMap.value > 1) valueSliderMap.value --;  
}

const hideALL = () => {
  showOnlyPMPAP.value     = false;    
  showOnlyPMPSM.value     = false;     showRajantwoPMPSMwoPMPAP.value = false;  
  showOnlyRajant.value    = false;   

  showSMandRajant.value   = false;     showRajantwoPMPSM.value = false;
  showSMandAP.value       = false;     showRajantwoPMPAP.value = false;
  showAPandRajant.value   = false;     showPMPSMwoRajant.value = false;
  showAPandSM.value       = false;     showPMPSMwoPMPAP.value  = false;
  showRajantandAP.value   = false;     showPMPAPwoPMPSM.value  = false;
  showRajantandSM.value   = false;     showPMPAPwoRajant.value = false;    
}


const showMarkerFunction = async (showitems:string) => {

  let functOBJ = showitems === '' ? 'hideALL' : showitems;

  const functionsItems:any = {
      'SM'        : () => { 
          hideALL();
          showOnlyPMPSM.value     = true;
      },
      'AP'        : () => {
          hideALL();
          showOnlyPMPAP.value     = true;
      },
      'RAJ'       : () => {
          hideALL();
          showOnlyRajant.value    = true;
      },
      'SMAP'      : () => {
          hideALL();
          showSMandAP.value       = true;
          showPMPSMwoPMPAP.value  = true;
          showPMPAPwoPMPSM.value  = true;
      },
      'APRAJ'     : () => {
          hideALL();
          showAPandRajant.value   = true;
          showRajantwoPMPAP.value = true;
          showPMPAPwoRajant.value = true;
      },
      'SMRAJ'     : () => {
          hideALL();
          showSMandRajant.value   = true;
          showPMPSMwoRajant.value = true;
          showRajantwoPMPSM.value = true;
      },
      'SMAPRAJ'   : () => {
          hideALL();
          showSMandRajant.value   = true;
          showSMandAP.value       = true;
          showPMPSMwoRajant.value = true;
          showAPandRajant.value   = true;
          showPMPAPwoRajant.value = true;
          showRajantwoPMPSMwoPMPAP.value = true;
      },
      'hideALL'   : () => {
          hideALL()
          showPolylines(true,true);
      }
  }
  functionsItems[functOBJ]()

}


watch(valueSliderMap, (v:number) => {
  let dateSelected = arrayDatesClustering.value[v-1]
  getFilterByHours(dateSelected)
})

watch(showRajant, (getBoolean:boolean) => {
  let getRaj  = getBoolean ? 'RAJ' : '';
  let getSM   = showPMPSM.value ? 'SM' : '';
  let getAP   = showPMPAP.value ? 'AP' : '';
  let finalObj = `${getSM}${getAP}${getRaj}`
  showMarkerFunction(finalObj)
})
watch(showPMPSM, (getBoolean:boolean) => {

  let getRaj  = showRajant.value ? 'RAJ' : '';
  let getSM   = getBoolean ? 'SM' : '';
  let getAP   = showPMPAP.value ? 'AP' : '';

  let finalObj = `${getSM}${getAP}${getRaj}`
  showMarkerFunction(finalObj);
})

watch(showPMPAP, (getBoolean:boolean) => {

  let getRaj  = showRajant.value ? 'RAJ' : '';
  let getSM   = showPMPSM.value ? 'SM' : '';
  let getAP   = getBoolean ? 'AP' : '';

  let finalObj = `${getSM}${getAP}${getRaj}`
  showMarkerFunction(finalObj)
})

watch(showOnlyNotOk, (getBoolean:boolean) =>{
  showRajant.value            = !getBoolean;
  showPMPSM.value             = !getBoolean;
  showPMPAP.value             = !getBoolean;
  showPolilynesBoolean.value  = !getBoolean;
  showPolylines(true,getBoolean);
})

const changezoom = (e:number) => {
  zoomMap.value = e
}

const changeCenter = (center:any) => {
  centerMap.value = center;
}

onMounted(async()=>{
  getAllHaulTrucks();
  // finalURL.value = nameProject.value + '/tiles/{z}/{x}/{y}.png';  
