<template>
    <div class="ma-4">
        <v-row v-if="showGraphs" class="ma-0">

            <!-- SNRVERTICAL Component -->
            <v-col cols="12" sm="12" md="6" lg="6" xl="6" class="pa-0 pr-sm-2 pr-md-2 pr-lg-2 pr-xl-2">
                <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <SingleChart v-if="arraySNRVertical.length !== 0" :name="'SNRVertical'" :first-array="arraySNRVertical" title="SNRVertical" :color="CONSTCOLORS.COLOR_HTML.single"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>

            <!-- SNRHORIZONTAL Component -->
            <v-col cols="12" sm="12" md="6" lg="6" xl="6" class="pa-0 pl-sm-2 pl-md-2 pl-lg-2 pl-xl-2">
                <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <SingleChart v-if="arraySNRHorizont.length !== 0" :name="'SNRHorizontal'" :first-array="arraySNRHorizont" title="SNRHorizontal" :color="CONSTCOLORS.COLOR_HTML.single"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>
        </v-row>

        <!-- LINKRADIOHORIZONTAL Component -->
        <v-sheet v-if="showGraphs" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
            <SingleChart v-if="arrayRadioHoriz.length !== 0" :name="'linkradiohoriz'" :first-array="arrayRadioHoriz" title="linkradiohoriz" :color="CONSTCOLORS.COLOR_HTML.single"/>
            <v-sheet v-else class="text-center" color="transparent">
                <Prog_circular />
            </v-sheet>
        </v-sheet>

        <!-- LINKRADIOVERTICAL Component -->
        <v-sheet v-if="showGraphs" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
            <SingleChart v-if="arrayRadioVerti.length !== 0" :name="'linkradiovert'" :first-array="arrayRadioVerti" title="linkradiovert" :color="CONSTCOLORS.COLOR_HTML.single"/>
            <v-sheet v-else class="text-center" color="transparent">
                <Prog_circular />
            </v-sheet>
        </v-sheet>

        <!-- THROUGHPUT Component -->
        <v-sheet v-if="showGraphs" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
            <SingleChart v-if="arrayThroughput.length !== 0" :name="'throughput'" :first-array="arrayThroughput" title="throughput" :color="CONSTCOLORS.COLOR_HTML.single"/>
            <v-sheet v-else class="text-center" color="transparent">
                <Prog_circular />
            </v-sheet>
        </v-sheet>
    </div>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref } from 'vue'
import axios            from 'axios';
import { useDisplay }   from 'vuetify';

// COMPONENTS
import Prog_circular    from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleChart      from '@/components/SmartLink/charts/SingleChart.vue';

// CONSTANTS
import CONSTCOLORS      from '@/constants/colors';
import CONSTAPIS        from '@/constants/apis';

import { getArrayOrSingle } from '@/functions/project'

// INTERFACES
import { PMPSM }        from '@/interfaces/SmartLink/Graphs';
import { showConsoleError } from '@/functions/errors';

const props = defineProps({
    title   :{ required:true },
    ip      :{ required:true, type:String },
})

const { xs }                            = useDisplay();
const arrayRadioHoriz:Ref<number[][]>   = ref([]);
const arrayRadioVerti:Ref<number[][]>   = ref([]);
const arrayThroughput:Ref<number[][]>   = ref([]);
const arraySNRVertical:Ref<number[][]>  = ref([]);
const arraySNRHorizont:Ref<number[][]>  = ref([]);
const projectName = process.env.VUE_APP_NAME_PROJECT;

/**
 * Si trae valores la API, muestra su grafico.
 */
const showGraphs    = ref(true);      


/**
 * get All Data from API to Graph (linkradiohoriz, linkradiovert, throughput, snr)
 */
const getDataGraph = (type:string) =>{

    showGraphs.value    = true;
    
    const params = {
        ip      : props.ip,
        type    : type
    }
    axios.get(CONSTAPIS.APIS.topology.getGraphTopo,{params:params, timeout : 10000}).then((item) => {
        if(item.data.length === 0){
            showGraphs.value    = false;
        }
        else {
            item.data.forEach((element:PMPSM)=>{
                let tiempo          = new Date(element.fecha??'');
                let milliseconds    = tiempo.getTime();

                if(projectName === 'SmartLinkCananea'){
                    let allSNR          = JSON.parse(element.snr??`{"V" : 0, "H" : 0, "PER" : 0}`);

                    let finalThroughput = getArrayOrSingle(allSNR['PER'],0);
                    let SNRV            = getArrayOrSingle(allSNR['V'],3);
                    let SNRH            = getArrayOrSingle(allSNR['H'],3);
                    let RADIOrx         = getArrayOrSingle(allSNR['V'],1);
                    let RADIOtx         = getArrayOrSingle(allSNR['H'],0);

                    arrayRadioHoriz.value.push([milliseconds,RADIOrx??0]);
                    arrayRadioVerti.value.push([milliseconds,RADIOtx??0]);
                    arrayThroughput.value.push([milliseconds,finalThroughput]);
                    arraySNRVertical.value.push([milliseconds,SNRV??0]);
                    arraySNRHorizont.value.push([milliseconds,SNRH??0]);
                }
                else{
                    // Convierte a JSON la variable "throughput"
                    // Si no existe, el valor por default es {'inthroughputbytes' : 0}
                    let prevThroughput  = JSON.parse(element.throughput??'{"inthroughputbytes":0}');
    
                    // Entramos a la llave "inthroughputbytes" ya que es donde se encuentra el valor
                    let finalThroughput = parseFloat(prevThroughput.inthroughputbytes)
    
                    // Convierte a JSON la variable "snr"
                    // Si no existe, el valor por default es {"V": 0, "H":0}
                    let allSNR          = JSON.parse(element.snr??`{"V": 0, "H":0}`);
    
                    // Como trae valores simples o en un array, primero se valida llamando a la funcion "getArrayOrSingle"
                    // Si existe y si es un objeto (un array), se captura el primer valor del array,
                    // Caso contrario se coloca el valor único que tiene
                    let SNRV            = getArrayOrSingle(allSNR['V'],0);
                    let SNRH            = getArrayOrSingle(allSNR['H'],0);
    
                     // Convierte a JSON la variable "link_radio"
                    // Si no existe, el valor por default es {"tx": 0, "rx":0}
                    let allRADIO        = JSON.parse(element.link_radio??`{"tx": 0, "rx":0}`);
    
                    // Como trae valores simples o en un array, primero se valida llamando a la funcion "getArrayOrSingle"
                    // Si existe y si es un objeto (un array), se captura el primer valor del array,
                    // Caso contrario se coloca el valor único que tiene
                    let RADIOrx         = getArrayOrSingle(allRADIO['rx'],0);
                    let RADIOtx         = getArrayOrSingle(allRADIO['tx'],0);
    
                    arrayRadioHoriz.value.push([milliseconds,RADIOrx??0]);
                    arrayRadioVerti.value.push([milliseconds,RADIOtx??0]);
                    arrayThroughput.value.push([milliseconds,finalThroughput]);
                    arraySNRVertical.value.push([milliseconds,SNRV??0]);
                    arraySNRHorizont.value.push([milliseconds,SNRH??0]);
                }
            })
        }
    })
    .catch(function (error) {        
        showGraphs.value    = false;
        showConsoleError(error);
    });
}

onMounted(async() => {
    getDataGraph('cambium_data');
})

</script>
