const getPredictSNRData = async () => {
    store.commit('showOverlay',true);
    showError.value     = false;
    showCard.value      = true;
    arrayPredictSNR.value = [];

    await axios.get(CONSTAPIS.APIS.predict.getAllSNRData, { timeout: 10000 }).then((res)=>{

        arrayPredictSNR.value = res.data;
        store.commit('showOverlay',false);
        if( arrayPredictSNR.value.length > 0) {
            showError.value     = false;
            showCard.value      = true;
        } 
        else{
            showError.value     = true;
            showCard.value      = false;
        }
    })
    .catch(function (error) {
        showError.value     = true;
        showCard.value      = false;
        showConsoleError(error);
    });
}


const showDialog = async (ip:string, name:string) => {
    titleDialog.value   = name;
    dialog.value        = true;
    valorPredVER.value  = [];
    valorPredHOR.value  = [];
    valorRealVER.value  = [];
    valorRealHOR.value  = [];
    itemsVER.value      = [];
    itemsHOR.value      = [];

    let params = { ip: ip };

    await axios.get(CONSTAPIS.APIS.predict.getPredSNRByIP, {params:params}).then((res)=>{

        let itemData = res.data;

        itemData.forEach((item:SNR_PREDICT_GRAPH) =>{
            let itemTime        = new Date(item.fecha);
            let milliseconds    = itemTime.getTime();
            let values          = item.snr;
            if(item.causa.toUpperCase() === 'PREDICT SNR V') { valorPredVER.value.push([milliseconds,values]); } 
            if(item.causa.toUpperCase() === 'PREDICT SNR H') { valorPredHOR.value?.push([milliseconds,values]); }
        })
            if(valorPredVER.value.length > 0) {
                let fechaVer        = new Date(valorPredVER.value.slice(-1)[0][0] + (300 * 60 * 1000) );
                titleDateVER.value  = fechaVer.toLocaleString( "es-PE");
                titleValueVER.value = (valorPredVER.value.slice(-1)[0][1]).toString();
            }
            
            if(valorPredHOR.value.length > 0) {
                let fechaHor        = new Date(valorPredHOR.value.slice(-1)[0][0] + (300 * 60 * 1000) );
                titleDateHOR.value  = fechaHor.toLocaleString("es-PE");
                titleValueHOR.value = (valorPredHOR.value.slice(-1)[0][1]).toString();
            }
    })
    
    await axios.get(CONSTAPIS.APIS.predict.getRealSNRByIP,{params:params}).then((res) => {

        let itemData = res.data;

        itemData.forEach((item:SNR_REAL_GRAPH) =>{
            let itemTime        = new Date(item.fecha);
            let milliseconds    = itemTime.getTime();

            if(projectName === 'SmartLinkCananea'){
                let allSNR = JSON.parse(item.snr??`{'H' : 0, 'V' : 0, 'PER' : 0}`);

                let SNRV        = getArrayOrSingle(allSNR['V'],3);
                let SNRH        = getArrayOrSingle(allSNR['H'],3);
    
                valorRealVER.value.push([milliseconds,SNRV]);
                valorRealHOR.value.push([milliseconds,SNRH]);
            }
            else{
                let allSNR      = JSON.parse(item.snr??`{"V": 0, "H":0}`);

                let SNRV        = getArrayOrSingle(allSNR['V'],0);
                let SNRH        = getArrayOrSingle(allSNR['H'],0);
    
                valorRealVER.value.push([milliseconds,SNRV]);
                valorRealHOR.value.push([milliseconds,SNRH]);
                
            }

        })
    })

    await axios.get(CONSTAPIS.APIS.predict.getTabSNRByIP,{params:params}).then((res) => {

        let itemData            = res.data;

        itemData.forEach((item:SNR_PREDICT_TABLE) =>{

            let objSNR = {
                fecha   : item.fecha,
                ip      : item.ip,
                status  : item.status,
                causa   : item.causa,
                value   : item.snr,
            }

            if(item.causa.toUpperCase() === 'PREDICT SNR V') { itemsVER.value.push(objSNR); }
            if(item.causa.toUpperCase() === 'PREDICT SNR H') { itemsHOR.value.push(objSNR); }
        })
    })
}

onMounted(async () => {
    await getPredictSNRData();  
})


