<template>
    <v-card class="defaultCard" color="bgtopology" elevation="0">
        <span class="titlePages">{{ t('adminPage.titleCSVview') }}</span>
        <v-file-input 
        v-model="rajant"
        clearable 
        label="MESH" 
        density="compact"
        @change="handleFileRajantUpload" 
        @click:clear="clearData('rajant')"
        class="mt-5"
        >
        </v-file-input>
        <v-file-input 
        v-model="cambium"
        class="mt-5" 
        clearable 
        label="PMP" 
        density="compact"
        @change="handleFileCambiumUpload" 
        @click:clear="clearData('cambium')"
        >
        </v-file-input>
        <v-row class="mt-5 mb-3">
            <v-col cols="6">
                <v-btn 
                :disabled="fileItemRajant.length === 0"
                block
                color="bgCircular"
                @click="rajantEvent"
                >
                    MESH
                </v-btn>
            </v-col>
            <v-col cols="6">
                <v-btn                 
                :disabled="fileItemCambium.length === 0"
                block
                color="bgCircular"
                @click="cambiumEvent"
                >
                    PMP
                </v-btn>
            </v-col>
        </v-row>
        <v-snackbar v-model="snackbar" >
            {{ t('adminPage.titleSnak') }}
            <template v-slot:actions>
                <v-btn
                color="pink"
                variant="text"
                @click="snackbar = false"
                >
                    {{ t('adminPage.closeBtn') }}
                </v-btn>
            </template>
        </v-snackbar>
        
    </v-card>
</template>

<script setup lang="ts">
import { ref, onMounted, Ref } from 'vue';
import { read, utils }  from 'xlsx';
import axios            from 'axios';
import { useI18n }      from 'vue-i18n';

// CONSTANTS
import CONSTAPIS        from '@/constants/apis';

// INTERFACES
import { STRINGS }      from '@/interfaces/SmartLink/General';
import { GENERAL_EQUIPAMENT, IP_EQUIPAMENT } from '@/interfaces/SmartLink/Equips';

const { t }                                             = useI18n()
const rajant                                            = ref();
const cambium                                           = ref();
const fileItemRajant:Ref<GENERAL_EQUIPAMENT[]>          = ref([]);
const fileItemNameRajant:Ref<GENERAL_EQUIPAMENT[]>      = ref([]);
const fileItemCambium:Ref<GENERAL_EQUIPAMENT[]>         = ref([]);
const fileItemNameCambium:Ref<GENERAL_EQUIPAMENT[]>     = ref([]);
const fileItemInsertRajant:Ref<GENERAL_EQUIPAMENT[]>    = ref([]);
const fileItemInsertCambium:Ref<GENERAL_EQUIPAMENT[]>   = ref([]);
const snackbar                                          = ref(false);
const allEquipment:Ref<IP_EQUIPAMENT[]>                 = ref([]);

const TYPE_EQUIP:STRINGS        = {
    'AP Movil'          :   'Solar Trailer',
    'Camion'            :   'Haultruck',
    'Pala'              :   'Shovel',
    'Perforadora'       :   'Driller',
    'Cargador Frontal'  :   'Front Loader',
    'Pipa Agua'         :   'Watertruck',
    'AP Fijo'           :   'Tower'
}

const SUBTYPE_EQUIP:STRINGS     = {
    'Pala'              :   'Acceso',
    'AP Movil'          :   'Acceso',
    'AP Fijo'           :   'Acceso',
    'Perforadora'       :   'Cliente',
    'Camion'            :   'Cliente',
    'Cargador Frontal'  :   'Cliente',
    'Pipa Agua'         :   'Cliente',
}


const handleFileRajantUpload = (event:any) => {

    if(event.target.files[0] !== undefined){
        fileItemRajant.value = [];
        const file = event.target.files[0];
        const reader = new FileReader();

        reader.onload = (e:any) => {
            const data          = new Uint8Array(e.target.result);
            const workbook      = read(data, { type: 'array' });
            const worksheet     = workbook.Sheets[workbook.SheetNames[0]];
            const jsonData      = utils.sheet_to_json(worksheet, { header: 1 });
            jsonData.forEach((elem:any) => {
                if(elem[3]?.split('.')[0] === '10'){
                    let ipfinal     = elem[3];
                    let hostname    = elem[1];  
                    let tipo        = TYPE_EQUIP[elem[6].trim()]||elem[6];       
                    let subtipo     = SUBTYPE_EQUIP[elem[6].trim()]||elem[6]       
                    let isMatch     = false;
                    let isMatchName = false;
                    allEquipment.value.find(({ip,name}:IP_EQUIPAMENT)=>{
                        if(name == hostname){ isMatchName = true }
                        if(ip == ipfinal)   { isMatch = true }
                    })
                    if(isMatch){
                        let objFile = {
                            ip          :   ipfinal,
                            hostname    :   hostname,
                        }
                        fileItemRajant.value.push(objFile);
                    }
                    else{
                        if(isMatchName && !isMatch){
                            let objFileName = {
                                ip              :   ipfinal,
                                hostname        :   hostname,
                                tipo            :   tipo,
                                subtipo         :   subtipo,
                            }
                            fileItemNameRajant.value.push(objFileName);
                        } else{
                            let objFile:GENERAL_EQUIPAMENT = {
                                elemid          :   0,
                                ip              :   ipfinal,
                                hostname        :   hostname,
                                marca           :   'Rajant',
                                modelo          :   ' ',
                                tipo            :   tipo,
                                subtipo         :   subtipo,
                                serial          :   ' ',
                                observacion     :   ' ',
                                ubicacion       :   ' ',
                                padre           :   0,
                                gpsLat          :   ' ',
                                gpsLong         :   ' ',
                                gpsAlt          :   ' '
                            }
                            fileItemInsertRajant.value.push(objFile);
                        }
                    }
                }
            })
            if(fileItemRajant.value.length === 0) snackbar.value = true;
        };
        reader.readAsArrayBuffer(file);
    }else{
        fileItemRajant.value = [];
    }
}

const handleFileCambiumUpload = (event:any) => {
    
    if(event.target.files[0] !== undefined){
        fileItemCambium.value = [];

        const file = event.target.files[0];
        const reader = new FileReader();

        reader.onload = (e:any) => {

            const data      = new Uint8Array(e.target.result);
            const workbook  = read(data, { type: 'array' });
            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
            const jsonData  = utils.sheet_to_json(worksheet, { header: 1 });

            jsonData.forEach((elem:any) => {

                if(elem[5]?.split('.')[0] === '10' && typeof elem[5] === 'string' && elem[5] !== ''){

                    let ipfinal     = elem[5];
                    let hostname    = elem[4];
                    let modelo      = elem[7];
                    let serial      = elem[8];
                    let isMatch     = false;
                    let isMatchName = false;

                    allEquipment.value.find(({ip,name}:IP_EQUIPAMENT)=>{
                        if(ip == ipfinal){
                            isMatch = true;
                        }
                        if(name == hostname){
                            isMatchName = true;
                        }
                    })

                    if(isMatch){
                        let objFile = {
                            ip          :   ipfinal,
                            hostname    :   hostname,
                            modelo      :   modelo,
                            serial      :   serial
                        }
                        fileItemCambium.value.push(objFile);
                    } else{
                        if(isMatchName && !isMatch){
                            let objFileName = {
                                ip              :   ipfinal,
                                hostname        :   hostname,
                                modelo          :   modelo,
                                serial          :   serial,
                                tipo            :   modelo.split(' ').slice(-1) == 'SM' ? 'PMP' : 'PMP-AP',
                            }
                            fileItemNameCambium.value.push(objFileName);
                        } else{
                            let objFile:GENERAL_EQUIPAMENT = {
                                elemid      :   0,
                                ip          :   ipfinal,
                                hostname    :   hostname,
                                modelo      :   modelo,
                                serial      :   serial,
                                marca       :   'Cambium',
                                tipo        :   modelo.split(' ').slice(-1) == 'SM' ? 'PMP' : 'PMP-AP',
                                subtipo     :   'Distribucion',
                                observacion :   ' ',
                                ubicacion   :   ' ',
                                padre       :   0,
                                gpsLat      :   ' ',
                                gpsLong     :   ' ',
                                gpsAlt      :   ' '
                            }
                            fileItemInsertCambium.value.push(objFile);
                        }
                    }

                    let objFile = {
                        ip          :   ipfinal,
                        hostname    :   hostname,
                        modelo      :   elem[7],
                        serial      :   elem[8]
                    }
                    fileItemCambium.value.push(objFile);
                }
            })

            if(fileItemCambium.value.length === 0) snackbar.value = true;
        };
        reader.readAsArrayBuffer(file);
    }else{
        fileItemCambium.value = [];
    }
}

const clearData = (type:string) => {
    if(type === 'rajant') fileItemRajant.value = []
    if(type === 'cambium') fileItemCambium.value = []
}


const rajantEvent = async () => {
    await axios.put(CONSTAPIS.APIS.csv.editDataInven,fileItemRajant.value).then((response)=>{
        if(response.status === 200){
            rajant.value                = [];
            fileItemRajant.value        = [];
        }                 
    })

    await axios.put(CONSTAPIS.APIS.csv.editDataByName,fileItemNameRajant.value).then((response)=>{
        if(response.status === 200){
            rajant.value                = [];
            fileItemNameRajant.value    = [];
        }                 
    })

    await axios.post(CONSTAPIS.APIS.csv.insertDataInven,fileItemInsertRajant.value).then((response)=>{
        if(response.status === 200){
            rajant.value                  = [];
            fileItemInsertRajant.value    = [];
        }                 
    })
}

const cambiumEvent = async () => {
    await axios.put(CONSTAPIS.APIS.csv.editDataInven,fileItemCambium.value).then((response)=>{
        if(response.status === 200){
            cambium.value               = [];
            fileItemCambium.value       = [];
        } 
    })

    await axios.put(CONSTAPIS.APIS.csv.editDataByName,fileItemNameCambium.value).then((response)=>{
        if(response.status === 200){
            cambium.value               = [];
            fileItemNameCambium.value   = [];
        } 
    })
    
    await axios.post(CONSTAPIS.APIS.csv.insertDataInven,fileItemInsertCambium.value).then((response)=>{
        if(response.status === 200){
            cambium.value               = [];
            fileItemInsertCambium.value = [];
        }                 
    })
}

onMounted(async() => {
    await axios.get(CONSTAPIS.APIS.csv.getDataInven).then((res)=>{
        allEquipment.value = res.data;
    })
})

</script>