<template>
    <v-card class="card_dialog" color="background"> 
        <v-row class="ma-0" justify="space-between" style="align-items: self-end !important;" >
            <v-col cols="10">
                <span :class="xs ? 'textDashboard' : 'subtitleDashboard'"> {{ t('title.graphTitle')}} {{ name }}</span>
            </v-col>
            <v-col cols="1">
                <v-btn icon="mdi-close" variant="plain" @click="$emit('dialog',false)"></v-btn>
            </v-col>
        </v-row>
        <v-divider class="mt-7"></v-divider>
        <v-sheet color="transparent">
            <PrimaryGraphs :ip='ip' :typesel='typeSel'  :name='name' :status="status" />
            <pmpAp :ip="ip" :title="'typeValue'" />
            <statusLog v-if="typeSel !== CONSTSTRINGS.STRINGS_HTML.pmpap" :ip='ip' :typesel='typeSel'  :name='name'/>
            <!-- <SNMPGraphs v-if="marca === CONSTSTRINGS.STRINGS_HTML.Mikrotik || marca === CONSTSTRINGS.STRINGS_HTML.cisco || typeSel === CONSTSTRINGS.STRINGS_HTML.ptp" :ip='ip'/> -->
            <PmpSm v-if="typeSel === CONSTSTRINGS.STRINGS_HTML.quality_lap || typeSel === CONSTSTRINGS.STRINGS_HTML.pmpsm"  :ip="ip" :title="'typeValue'"/>
            <AnotherGraphs v-if="typeSel !== CONSTSTRINGS.STRINGS_HTML.latency && typeSel !== CONSTSTRINGS.STRINGS_HTML.quality_lap && typeSel !== CONSTSTRINGS.STRINGS_HTML.pmpsm && typeSel !== CONSTSTRINGS.STRINGS_HTML.pmpap" :ip="ip" :title="typeSel"  /> 
            <instameshGraphsVue v-if="typeSel !== CONSTSTRINGS.STRINGS_HTML.pmpap && typeSel !== CONSTSTRINGS.STRINGS_HTML.pmpsm && typeSel !== CONSTSTRINGS.STRINGS_HTML.ptp" :ip="ip" :typeitem="typeSel" :nameitem="name.split('-')[1]"/>
        </v-sheet>  
    </v-card>
</template>

<script setup lang="ts">
import { useDisplay }       from 'vuetify';

// COMPONENTS
import PrimaryGraphs        from './primaryGraphs.vue';
import pmpAp                from './pmp-ap.vue';
import statusLog            from './status-log.vue';
import PmpSm                from './pmp-sm.vue';
import AnotherGraphs        from './anotherGraphs.vue';
// import SNMPGraphs           from './snmpGraphs.vue';
import instameshGraphsVue   from '@/components/SmartLink/dialog/instameshGraphs.vue';
import { useI18n }          from 'vue-i18n';


//CONSTANTS
import CONSTSTRINGS         from '@/constants/strings'

defineProps({
    marca       : { type:String },
    status      : { type:String },
    ip          : { type:String, required:true },
    typeSel     : { type:String, required:true },
    name        : { type:String, required:true },
})

const { t }     = useI18n()
const { xs }    = useDisplay();

</script>