})

const clearLayerSource = (layer:string, source:string) => {
    if(map.getLayer(layer)) { map.removeLayer(layer); }
    if(map.getSource(source)) { map.removeSource(source); }
}

const watchLayer = (selectedVar: Ref<boolean>, source: string, finalObject: Ref<any>, layer: string) => {
    watch(selectedVar, (state) => {
        if (state) {
            setSourceAndLayer(source, finalObject.value, layer);
        } else {
            clearLayerSource(layer, source);
        }
    });
}; 

watchLayer(selectedETH0,  'SourceETH0', finalETH0Object, 'LayerETH0');
watchLayer(selectedRPTPEER,  'SourceRPTPEER', finalRPTPEERObject, 'LayerRPTPEER');
watchLayer(selectedWLAN0, 'SourceWLAN0', finalWLAN0Object, 'LayerWLAN0');
watchLayer(selectedWLAN1, 'SourceWLAN1', finalWLAN1Object, 'LayerWLAN1');
watchLayer(selectedWLAN2, 'SourceWLAN2', finalWLAN2Object, 'LayerWLAN2');
watchLayer(selectedWLAN3, 'SourceWLAN3', finalWLAN3Object, 'LayerWLAN3');
watchLayer(selectedLATENCIA, 'SourceLATENCIA', finalLATENCIAObject, 'LayerLATENCIA');

