                            <extrude>1</extrude>
                            <altitudeMode>relativeToGround</altitudeMode>
                            <outerBoundaryIs>
                                <LinearRing>
                                <coordinates>
                                    ${finalCoord1[0]},${finalCoord1[1]},20
                                    ${finalCoord2[0]},${finalCoord2[1]},20
                                    ${finalCoord3[0]},${finalCoord3[1]},20
                                    ${finalCoord4[0]},${finalCoord4[1]},20
                                    ${finalCoord5[0]},${finalCoord5[1]},20
                                </coordinates>
                                </LinearRing>
                            </outerBoundaryIs>
