<template>
    <div>
        <Chart :class="isactive ? 'borderActive' : ''" :constructorType="'stockChart'" class="hc" :options="finalOptions" ref="chartRef"></Chart>
    </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted }    from 'vue';
import {Chart}              from 'highcharts-vue';
import { useI18n }          from 'vue-i18n';
import { getOptionsInGraphs } from '@/functions/project';
import CONSTCOLORS          from '@/constants/colors'
 
const props = defineProps({
    array1:{ required:true, type:Array },
    array2:{ required:true, type:Array },
    array3:{ required:true, type:Array },
    array4:{ required:true, type:Array },
    isactive:{ type:Boolean, default: false }
})
        
const { t }                 = useI18n();


const data1         = computed(() => props.array1);
const data2         = computed(() => props.array2);
const data3         = computed(() => props.array3);
const data4         = computed(() => props.array4);

const oksLabel       = computed(() =>  t('labels.ok_pl'));
const aleLabel       = computed(() =>  t('labels.alert_pl'));
const alaLabel       = computed(() =>  t('labels.alarm_pl'));
const dowLabel       = computed(() =>  t('labels.down_pl'));

const seriesValue = [
        {
            name: dowLabel.value,
            data: data4.value,
            color:CONSTCOLORS.COLOR_HTML.grey,
        },
        {
            name: alaLabel.value,
            data: data2.value,
            color: CONSTCOLORS.COLOR_HTML.red,
        },
        {
            name: aleLabel.value,
            data: data3.value,
            color: CONSTCOLORS.COLOR_HTML.yellow,
        },
        {
            name: oksLabel.value,
            data: data1.value,
            color: CONSTCOLORS.COLOR_HTML.green,
            visible: false
        },
    ]

/* eslint-disable */

/**
 * getOptionsInGraphs(tipo, serie (areaspline=> para curvas, pero no stackea), titulo, subtitulo, ocultar leyenda, altura, tooltip, yaxis)
 */
const finalOptions:any = ref(getOptionsInGraphs('area',seriesValue,'','',true, 460, {shared:true}, {}));




const chartRef = ref<any>();
const duplicateChart = ref();

onMounted(async () => {
    setTimeout(() => {
        duplicateChart.value = chartRef.value;
    }, 500);
});

onUnmounted(() => {
    if(duplicateChart.value){
        duplicateChart.value.chart.destroy();
    }
});

</script>
<style>
.highcharts-legend-item text{
    fill: rgb(var(--varColorText)) !important;
}
.borderActive{
    border: 3px solid rgba(255,255,255,.8);
    border-radius: 20px;
}
</style>