<template>
    <v-card class="defaultCard" color="bgtopology">
        <span class="titlePages"> {{ t('title.allLatency')}} </span>

        <v-row v-if="showCard" class="ma-0 mr-2" :justify="smAndDown ? 'center' : 'end'">
            <v-btn-toggle divided color="#E84FC4" rounded="2" group variant="outlined"  >
                <v-btn size="small" class="btnSlidersDashboard" @click="changeData(CONSTSTRINGS.STRINGS_TYPES[21])"> <span class="text_btns"> {{ t('labels.all') }}  </span>  </v-btn>
                <v-btn size="small" class="btnSlidersDashboard" @click="changeData(CONSTSTRINGS.STRINGS_TYPES[2])">  <span class="text_btns"> {{ t('labels.distribution') }}  </span>  </v-btn>
                <v-btn size="small" class="btnSlidersDashboard" @click="changeData(CONSTSTRINGS.STRINGS_TYPES[10])"> <span class="text_btns"> {{ t('labels.client') }}   </span>  </v-btn>
                <v-btn size="small" class="btnSlidersDashboard" @click="changeData(CONSTSTRINGS.STRINGS_TYPES[6])">  <span class="text_btns"> {{ t('labels.access') }}   </span>  </v-btn>
                <v-btn size="small" class="btnSlidersDashboard" @click="changeData(CONSTSTRINGS.STRINGS_TYPES[20])"> <span class="text_btns"> {{ t('labels.down') }}    </span>  </v-btn>
            </v-btn-toggle>
        </v-row>
        <v-row v-if="finalTableItems.length > 0  && showCard" justify="space-between" class="ma-0 mb-4">
            <v-col v-for="(item,i ) in finalLegend" :key="i">
                <rangeComponent
                    :principal-color="item.prinColor "
                    :secundary-color="item.secuColor "
                    :color-text-princ="item.colorText1"
                    :color-text-secun="item.colorText2"
                    :principal-text="item.prinText"
                    :secundary-text="item.secuText"
                    />
            </v-col>
        </v-row>
        <EasyDataTable
        v-if="finalTableItems.length > 0  && showCard"
        header-text-direction="left"
        table-class-name="customize-table"
        show-index
        :headers="headertable"
        :items="finalTableItems"
        :rows-per-page="100"
        @click-row="showDialogWithType"
        >
            <template #item-ip ="item">
                <div style="width: 90px;">
                    <span> {{ item.ip }} </span>
                </div>
            </template>
            <template #item-latencia ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.latencia }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        style="border-radius: 20px;"
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.latencia < 0 ? '#9E9E9E' : item.latencia <= 100 ? '#00FF00' : item.latencia <= 500 ? '#FFEE00' :'#FF0000'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>
        </EasyDataTable>
        <v-sheet v-if="showCircle" class="text-center" color="transparent">
            <Prog_circular />
        </v-sheet>
        <v-row  v-if="showError" class="ma-0 py-3">
            <h3> {{ t('noInformation')}} </h3>
        </v-row>
    </v-card>

    
    <!-- DIALOG COMPONENT -->
    <v-dialog v-model="dialog" width="85%">
        <DialogComponent
        v-if="dialog"
        :ip="selectedIp"
        :typeSel="selectedType"
        :name="selectedName"
        @dialog="closeDialog"
        />             
    </v-dialog>
</template>

<script setup lang="ts">
import { ref, computed, ComputedRef, onMounted, Ref } from 'vue';
import { useI18n }              from 'vue-i18n';
import { useDisplay }           from 'vuetify';
import axios                    from 'axios';

// COMPONENTS
import DialogComponent          from '@/components/SmartLink/dialog/dialogComponent.vue';
import Prog_circular            from '@/components/PrincipalComponents/Prog_circular.vue';
import rangeComponent           from '@/components/SmartLink/dashboard/rangeComponent.vue';


// CONSTANTS
import CONSTAPIS                from '@/constants/apis';
import CONSTSTRINGS             from '@/constants/strings';
import { FINAL_TYPE_LEGEND }    from '@/constants/legends';
import { FINAL_HEADERS_TABLE }  from '@/constants/tableHeaders'


// INTERFACES
import { LATENCY }              from '@/interfaces/SmartLink/Topology';
import { LEGEND }               from '@/interfaces/SmartLink/General';
import { showConsoleError } from '@/functions/errors';


const { t }                             = useI18n();
const { smAndDown }                     = useDisplay();
const finalTableItems:Ref<LATENCY[]>    = ref([]);
const arrayTodos:Ref<LATENCY[]>         = ref([]);
const arrayAcceso:Ref<LATENCY[]>        = ref([]);
const arrayDistri:Ref<LATENCY[]>        = ref([]);
const arrayClient:Ref<LATENCY[]>        = ref([]);
const arrayDowns:Ref<LATENCY[]>         = ref([]);

/**
 * Muestra el apartado de error, en vez de mostrar información o un espacio vacío. 
 * Generalmente es cuando desde la API no trae valores.
 */
const showError                         = ref(false);

/**
 * Mostrar los cards con info, si no tiene dato desde las API, oculta la tabla y los botones
 */
const showCard                          = ref(true);

/**
 * Mostrar el Loading
 */
const showCircle                        = ref(true);

const getAllLatencies = () => {
    showError.value     = false;
    showCard.value      = false;
    showCircle.value    = true;

    /**
     * Obtiene los valores de Latencia de la API: "getAllLatency"
     * De todos los equipos en los ultimos 15 minutos
     */
    axios.get(CONSTAPIS.APIS.kpi.getAllLatency, { timeout: 10000 }).then(resp => {
        let apiArray = resp.data;        
        showCircle.value    = false;
        if(apiArray.length > 0){
            showError.value     = false;
            showCard.value      = true;

            apiArray.forEach((item:LATENCY)=>{
                if(item.latencia === -1){
                    arrayDowns.value.push(item)
                    arrayTodos.value.push(item) 
                }
                else{
                    item.subtipo === CONSTSTRINGS.STRINGS_TYPES[6]  ? arrayAcceso.value.push(item) : null;
                    item.subtipo === CONSTSTRINGS.STRINGS_TYPES[10] ? arrayClient.value.push(item) : null;
                    item.subtipo === CONSTSTRINGS.STRINGS_TYPES[2]  ? arrayDistri.value.push(item) : null;
                    arrayTodos.value.push(item)
                }
            })
            finalTableItems.value = arrayTodos.value
        }
        else{
            showError.value     = true;
            showCard.value      = false;
        }
    })
    .catch(function (error) {
        showError.value     = true;
        showCard.value      = false;
        showConsoleError(error);
    });
}

/**
 * Muestra los valores en las tablas, dependiendo del tipo de Familia
 * También se aplica para todos los equipos y los equipos caídos
 * @param value Tipo de familia, todos o los caídos
 */
const changeData = (value:string) =>{
    value === CONSTSTRINGS.STRINGS_TYPES[6]     ? finalTableItems.value = arrayAcceso.value : null;
    value === CONSTSTRINGS.STRINGS_TYPES[10]    ? finalTableItems.value = arrayClient.value : null;
    value === CONSTSTRINGS.STRINGS_TYPES[2]     ? finalTableItems.value = arrayDistri.value : null;
    value === CONSTSTRINGS.STRINGS_TYPES[20]    ? finalTableItems.value = arrayDowns.value  : null;
    value === CONSTSTRINGS.STRINGS_TYPES[21]    ? finalTableItems.value = arrayTodos.value  : null;
}

/**
 * Mostrar el Dialogo
 */
 const dialog        = ref(false);

/**
 * IP del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedIp    = ref('');

/**
 * Nombre del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedName  = ref('');

/**
 * Tipo del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedType  = ref('');

/**
 * Captura toda la información del elemento seleccionado en la tabla
 * y se almacenan los valores de (ip, tipo, nombre, marca, estado) en las variables correspondientes
 * para luego abrir el dialog con la información gráfica y estadística del equipo en cuestión
 * @param item La fila seleccionada en la tabla
 */
const showDialogWithType = (item: LATENCY) => {
    selectedIp.value        = item.ip;
    selectedName.value      = item.name;
    selectedType.value      = item.tipo;
    dialog.value            = true
}

/**
 * Cierra el Diálogo mediante un EMIT del componente de Dialogo
 * @param value valor FALSO
 */
const closeDialog = (value:boolean) => {
    dialog.value = value;
}

/**
 * Captura de la Constante el tipo de Cabecera de Tabla que tiene, dependiendo del valor de la vista seleccionada
 */
const headerLatency = FINAL_HEADERS_TABLE[CONSTSTRINGS.STRINGS_VIS_PAGES.latency];

/**
 * Valor de los HEADERS Seleccionados, solo que no se realiza aquí la petición por errores en el Front.
 * Una vez ya se hace el pedido a la API, recién se captura dicha cabecera de tabla
 */
const headertable   = ref(headerLatency);

/**
 * Captura de la Constante el tipo de Leyenda que tiene, dependiendo del valor de la vista seleccionada
 */
 const legend:ComputedRef<LEGEND[]>      = computed(() => FINAL_TYPE_LEGEND['latency']);

/**
 * Valor de la Legenda Seleccionada, solo que no se realiza aquí la petición por errores en el Front.
 * Una vez ya se hace el pedido a la API, recién se captura dicha leyenda
 */
const finalLegend                       = ref(legend.value);

onMounted(() => {
    getAllLatencies()  
})

</script>