<template>
    <v-row class="mx-1 mt-8">
        <!-- Temperature and Voltage Components -->
        <v-sheet v-if="typeitem === CONSTSTRINGS.STRINGS_TYPES[11] || typeitem === CONSTSTRINGS.STRINGS_TYPES[12]  || typeitem === CONSTSTRINGS.STRINGS_TYPES[14] ">
            <!-- || typeitem === CONSTCOLORS.STRINGS_TYPES[13] -->
        </v-sheet>
        
        <v-sheet v-else color="transparent" width="100%">
            <v-col cols="12">
                <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <SingleChart v-if="temperatureValues.length !== 0" :name="'temperature'" :first-array="temperatureValues" :title="'Temperature'" color="#000000"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>
            <v-col cols="12">
                <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <SingleChart v-if="voltageValues.length !== 0" :name="'Voltage'" :first-array="voltageValues" :title="'Voltage'" :color="CONSTCOLORS.COLOR_HTML.green"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>

            <!-- Cost Graph-->
            <v-col cols="12">
                <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <SingleChart v-if="costValues.length !== 0" :name="'Cost'" :first-array="costValues" :title="'Cost'" :color="CONSTCOLORS.COLOR_HTML.green"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>
        </v-sheet>

        <!-- Cost Graph JR3 -->
        <v-col cols="12" v-if="nameitem === CONSTSTRINGS.STRINGS_HTML.JR3" >
            <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                <SingleChart v-if="costValues.length !== 0" :name="'Cost'" :first-array="costValues" :title="'Cost'" :color="CONSTCOLORS.COLOR_HTML.green"/>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
        </v-col>

        <!-- Wired Graph -->
        <v-col cols="12" v-if="showWiredGraph" >
            <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                <CompareCharts
                v-if="wiredrxValues.length !== 0"
                :subtitle="''"
                name-primary="rxTB" 
                :first-array="wiredrxValues" 
                :color-first="CONSTCOLORS.COLOR_HTML.compare1"
                :color-second="CONSTCOLORS.COLOR_HTML.compare2"
                name-secundary="txTB" 
                :second-array="wiredtxValues" 
                title='Wired Tx - Rx [eth0]'
                :typeChart="CONSTSTRINGS.STRINGS_HTML.instamesh" />
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
        </v-col>

        <v-row class="ma-0" v-if="typeitem === CONSTSTRINGS.STRINGS_TYPES[7] || typeitem === CONSTSTRINGS.STRINGS_TYPES[8] || (typeitem === CONSTSTRINGS.STRINGS_TYPES[9] && nameitem === CONSTSTRINGS.STRINGS_HTML.LX5)">
        

            <v-col cols="12" sm="12" md="6" lg="6" xl="6">
                <v-sheet v-if="showWlan0rxValues" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <CompareCharts
                    v-if="wlan0rxValues.length !== 0"
                    :subtitle="''"
                    name-primary="rxTB" 
                    :first-array="wlan0rxValues" 
                    :color-first="CONSTCOLORS.COLOR_HTML.compare1"
                    :color-second="CONSTCOLORS.COLOR_HTML.compare2"
                    name-secundary="txTB" 
                    :second-array="wlan0txValues" 
                    title='Wireless Tx - Rx [wlan0]' 
                    :typeChart="CONSTSTRINGS.STRINGS_HTML.instamesh"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>

            <v-col cols="12" sm="12" md="6" lg="6" xl="6">
                <v-sheet v-if="showWlan1rxValues" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <CompareCharts
                    v-if="wlan1rxValues.length !== 0"
                    :subtitle="''"
                    name-primary="rxTB" 
                    :first-array="wlan1rxValues" 
                    :color-first="CONSTCOLORS.COLOR_HTML.compare1"
                    :color-second="CONSTCOLORS.COLOR_HTML.compare2"
                    name-secundary="txTB" 
                    :second-array="wlan1txValues" 
                    title='Wireless Tx - Rx [wlan1]' 
                    :typeChart="CONSTSTRINGS.STRINGS_HTML.instamesh"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>

            <v-col cols="12" sm="12" md="6" lg="6" xl="6">
                <v-sheet v-if="showWlan2rxValues" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <CompareCharts
                    v-if="wlan2rxValues.length !== 0"
                    :subtitle="''"
                    name-primary="rxTB" 
                    :first-array="wlan2rxValues" 
                    :color-first="CONSTCOLORS.COLOR_HTML.compare1"
                    :color-second="CONSTCOLORS.COLOR_HTML.compare2"
                    name-secundary="txTB" 
                    :second-array="wlan2txValues" 
                    title='Wireless Tx - Rx [wlan2]' 
                    :typeChart="CONSTSTRINGS.STRINGS_HTML.instamesh"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>

            <v-col cols="12" sm="12" md="6" lg="6" xl="6">
                <v-sheet v-if="showWlan3rxValues" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4" color="bgtopology">
                    <CompareCharts
                    v-if="wlan3rxValues.length !== 0"
                    :subtitle="''"
                    name-primary="rxTB" 
                    :first-array="wlan3rxValues" 
                    :color-first="CONSTCOLORS.COLOR_HTML.compare1"
                    :color-second="CONSTCOLORS.COLOR_HTML.compare2"
                    name-secundary="txTB" 
                    :second-array="wlan3txValues" 
                    title='Wireless Tx - Rx [wlan3]' 
                    :typeChart="CONSTSTRINGS.STRINGS_HTML.instamesh"/>
                    <v-sheet v-else class="text-center" color="transparent">
                        <Prog_circular />
                    </v-sheet>
                </v-sheet>
            </v-col>
        </v-row>
        
    </v-row>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref } from 'vue';
import { useDisplay }   from 'vuetify';
import axios            from 'axios';

// COMPONENTS
import Prog_circular    from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleChart      from '@/components/SmartLink/charts/SingleChart.vue';
import CompareCharts    from '@/components/SmartLink/charts/CompareCharts.vue';

// CONSTANTS
import CONSTAPIS        from '@/constants/apis';
import CONSTCOLORS      from '@/constants/colors';
import CONSTSTRINGS     from '@/constants/strings';

// INTERFACES
import { INSTAMESH }    from '@/interfaces/SmartLink/Graphs';
import { showConsoleError } from '@/functions/errors';

const props = defineProps({
    ip          : { required:true, type:String },
    nameitem    : { required:true, type:String },
    typeitem    : { required:true, type:String },
})
        
const { xs }                               = useDisplay();

const params = { ip : props.ip }


const temperatureValues:Ref<number[][]>    = ref([]);
const voltageValues:Ref<number[][]>        = ref([]);
const costValues:Ref<number[][]>           = ref([]);

/**
 * Capturamos los valores de la API: "getAllTempDataByIP"
 */
const getTempDataGraph = async () =>{            

    await axios.get(CONSTAPIS.APIS.instamesh.getTempByIP,{ params : params,  timeout : 10000 }).then((item) => {
        item.data.forEach((element:INSTAMESH)=>{
            let time            = new Date(element.fecha??'');
            let milliseconds    = time.getTime();

            // Convierte a JSON la variable "wireless"
            // Si no existe, el valor por default es ""
            let valores         = JSON.parse(element.valores??'')

            let tempvalues      = (valores.temperature[0]??0)/100;
            let voltvalues      = (valores.voltage[0]??0)/100;

            temperatureValues.value.push([milliseconds,tempvalues]);
            voltageValues.value.push([milliseconds,voltvalues]);
        })
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}


const wlan0rxValues:Ref<number[][]>     = ref([]);
const wlan0txValues:Ref<number[][]>     = ref([]);
const wlan1rxValues:Ref<number[][]>     = ref([]);
const wlan1txValues:Ref<number[][]>     = ref([]);
const wlan2rxValues:Ref<number[][]>     = ref([]);
const wlan2txValues:Ref<number[][]>     = ref([]);
const wlan3rxValues:Ref<number[][]>     = ref([]);
const wlan3txValues:Ref<number[][]>     = ref([]);

const showWlan0rxValues                 = ref(true);
const showWlan1rxValues                 = ref(true);
const showWlan2rxValues                 = ref(true);
const showWlan3rxValues                 = ref(true);

/**
 * captura los valores de la API: "getAllWirelessDataByIP"
 */
const getWirelessDataGraph = async () =>{

    await axios.get(CONSTAPIS.APIS.instamesh.getWirelessByIP,{ params : params, timeout : 10000 }).then((item) => {    
        if(item.data.length > 0) {
            item.data.forEach((element:INSTAMESH)=>{  
                let time            = new Date(element.fecha??'');                   
                let milliseconds    = time.getTime();

                let costvaluesPrev  = 0;

                // Convierte a JSON la variable "wireless"
                // Si no existe, el valor por default es ""
                let instameshWireless  = JSON.parse(element.wireless??''); 
                let instameshWired     = JSON.parse(element.wired??''); 

                if(instameshWired.cost) { 
                    costvaluesPrev = instameshWired.cost 
                    costValues.value.push([milliseconds,costvaluesPrev]);
                }else{
                    costvaluesPrev = instameshWireless.cost ?  instameshWireless.cost : 0
                    costValues.value.push([milliseconds,costvaluesPrev]);
                }

                if(instameshWireless.wlan0){
                    let firtsArray      = parseFloat(((instameshWireless.wlan0.rxBytes??0)/(1024 * 1024)).toFixed(2));
                    let secondArray     = parseFloat(((instameshWireless.wlan0.txBytes??0)/(1024 * 1024)).toFixed(2));
                    wlan0rxValues.value.push([milliseconds,firtsArray]);
                    wlan0txValues.value.push([milliseconds,secondArray]);
                }     

                if(instameshWireless.wlan1){
                    let firtsArray      = parseFloat(((instameshWireless.wlan1.rxBytes??0)/(1024 * 1024)).toFixed(2));
                    let secondArray     = parseFloat(((instameshWireless.wlan1.txBytes??0)/(1024 * 1024)).toFixed(2));
                    wlan1rxValues.value.push([milliseconds,firtsArray]);
                    wlan1txValues.value.push([milliseconds,secondArray]);
                }         

                if(instameshWireless.wlan2){
                    let firtsArray      = parseFloat(((instameshWireless.wlan2.rxBytes??0)/(1024 * 1024)).toFixed(2));
                    let secondArray     = parseFloat(((instameshWireless.wlan2.txBytes??0)/(1024 * 1024)).toFixed(2));
                    wlan2rxValues.value.push([milliseconds,firtsArray]);
                    wlan2txValues.value.push([milliseconds,secondArray]);
                }       

                if(instameshWireless.wlan3){
                    let firtsArray      = parseFloat(((instameshWireless.wlan3.rxBytes??0)/(1024 * 1024)).toFixed(2));
                    let secondArray     = parseFloat(((instameshWireless.wlan3.txBytes??0)/(1024 * 1024)).toFixed(2));
                    wlan3rxValues.value.push([milliseconds,firtsArray]);
                    wlan3txValues.value.push([milliseconds,secondArray]);
                }       

                wlan0rxValues.value.length > 0 ? showWlan0rxValues.value = true : showWlan0rxValues.value = false;
                wlan1rxValues.value.length > 0 ? showWlan1rxValues.value = true : showWlan1rxValues.value = false;
                wlan2rxValues.value.length > 0 ? showWlan2rxValues.value = true : showWlan2rxValues.value = false;
                wlan3rxValues.value.length > 0 ? showWlan3rxValues.value = true : showWlan3rxValues.value = false;


                // if(instameshWired.cost) { 
                //     costvaluesPrev = instameshWired.cost 
                //     costValues.value.push([milliseconds,costvaluesPrev]);
                // }
                
                let firtsArray      = parseFloat(((instameshWired.eth0.rxBytes??0)/(1024 * 1024)).toFixed(2));
                let secondArray     = parseFloat(((instameshWired.eth0.txBytes??0)/(1024 * 1024)).toFixed(2));
                wiredrxValues.value.push([milliseconds,firtsArray]);
                wiredtxValues.value.push([milliseconds,secondArray]);
            })            
        }
        else{
            showWlan0rxValues.value = false
            showWlan1rxValues.value = false
            showWlan2rxValues.value = false
            showWlan3rxValues.value = false
        }   
    })

    .catch(function (error) {
        showConsoleError(error);
    });
}


const wiredrxValues:Ref<number[][]>     = ref([]);
const wiredtxValues:Ref<number[][]>     = ref([]);
const showWiredGraph                    = ref(true);

onMounted( async () => {
    if(props.typeitem === CONSTSTRINGS.STRINGS_TYPES[11]  || props.typeitem === CONSTSTRINGS.STRINGS_TYPES[12] || props.typeitem === CONSTSTRINGS.STRINGS_TYPES[14]){
        console.log();
    }else{
        getTempDataGraph();
    }
    getWirelessDataGraph();
})

</script>
